export function resolveQuery (query) {
  const ret = {}
  Object.keys(query).forEach((k) => {
    const val = query[k].trim()
    if (val) {
      ret[k] = val
    }
  })
  if (Object.keys(ret).length === 0) {
    return null
  }

  return ret
}

export function resolveSelectVal (value = {}, key) {
  const { data = {} } = value
  return typeof key === 'function' ? key(data) : data[key]
}

export function resolveImgTitle (title, len = 16) {
  if (/(jpg|jpeg|png|gif)$/.test(title)) {
    return title.slice(-len)
  }

  return title
}

export function remove (arr, key) {
  let idx
  if (typeof key === 'function') {
    idx = arr.findIndex(key)
  } else {
    idx = arr.indexOf(key)
  }

  if (idx >= 0) {
    arr.splice(idx, 1)
    return true
  }
}

export function updateItem (arr, oldItem, item) {
  const idx = arr.indexOf(oldItem)
  if (idx >= 0) {
    arr.splice(idx, 1, item)
  }
}


