<style lang="scss">
.sp-viewport {
  width: 100%;
  height: 100%;
  position: relative;
  // overflow: auto;
  background: #f8f8f8;
  .h-container,
  .v-container {
    background: #fff;
  }
  .viewport-container {
    position: absolute;
    width: 100%;
    height: 100%;
    overflow: auto;
  }
  .draw-wrap {
    position: absolute;
    width: 3000px;
    height: 6000px;
  }
  #draw-container {
    position: absolute;
    top: 80px;
    left: 50%;
    // bottom: 80px;
    min-height: 800px;
    margin-left: -80px;
    transform-origin: 50% 0;
    .widget-active {
      position: relative;
      &::after {
        content: '';
        display: inline-block;
        border: 1px solid #3091f2;
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        z-index: 1000;
        pointer-events: none;
      }
    }
  }
}
.preview-modal {
  .ivu-modal-body {
    padding: 0;
  }
}
</style>

<template>
  <div class="sp-viewport">
    <SketchRule
      v-if="renderRule"
      :lang="lang"
      :thick="thick"
      :scale="scale"
      :width="width"
      :height="height"
      :start-x="startX"
      :start-y="startY"
      :shadow="shadow"
      :hor-line-arr="lines.h"
      :ver-line-arr="lines.v"
      :corner-active="true"
      @handleLine="handleLine"
      @onCornerClick="handleCornerClick"
    />
    <!-- <drawBoard id="screens" @wheel.native="handleWheel" @scroll.native="handleScroll" /> -->

    <div
      id="viewport-container"
      ref="screensRef"
      class="viewport-container"
      @wheel="handleWheel"
      @scroll="handleScroll"
    >
      <div
        ref="containerRef"
        class="draw-wrap"
      >
        <draggable
          id="draw-container"
          :options="options"
          :style="[viewportBoardStyle, { backgroundColor: `${pageProps ? pageProps.backgroundColor : '#f4f4f4'}` }]"
        >
          <template v-for="(widget, index) in pagedata.widgets">
            <!-- {{pageProps}} -->
            <component
              :is="widget.type"
              v-if="widget.type != 'W0000'"
              :key="widget.uuid"
              class="widget"
              mode="design"
              :kindex="index"
              :value="widget"
              :page-props="pageProps"
              :zoom="zoom"
              :class="{
                'widget-active': isActive(widget),
                'widget-hover': hoverEle_uuid === widget.uuid
              }"
              @click.native="handleClickComponent($event, widget.uuid)"
              @mouseenter.native="handleMouseEnter(widget.uuid)"
              @mouseleave.native="handleMouseLeave(widget.uuid)"
            >
              <vue-draggable-resizable
                :draggable="false"
                :w="widget.width + widget.paddingLeft + widget.paddingRight"
                :h="widget.height + widget.paddingTop + widget.paddingBottom"
                :z="100"
                :handles="['mr','bm','ml']"
                @resizing="(left, top, width, height) => onResizingComponent(widget, left, top, width, height)"
              />
              <action-tools
                v-show="widget.uuid === hoverEle_uuid"
                ref="actionTools"
                :uuid="widget.uuid"
                :sindex="index"
              />
            </component>
          </template>
        </draggable>
      </div>
    </div>

    <!--  -->
    <Modal
      v-model="modeShow"
      class="preview-modal"
      fullscreen
      footer-hide
      title=""
      @on-visible-change="handleClosePreview"
    >
      <div>
        <component
          :is="wgt.type"
          v-for="(wgt, index) in pagedata.widgets"
          :key="`wgt-${index}`"
          mode="preview"
          :value="wgt"
          :page-props="pageProps"
        />
      </div>
    </Modal>
  </div>
</template>

<script>
import SketchRule from 'vue-sketch-ruler'
import draggable from 'vuedraggable'
import { log } from '@/utils'
import { mapState, mapMutations } from 'vuex'
// import drawBoard from './comps/draw-board'
import actionTools from './comps/action-tools'
export default {
  name: 'SpViewPort',
  //   mixins: [mixins],
  components: {
    SketchRule,
    // drawBoard,
    actionTools,
    draggable
  },
  props: {},
  data () {
    return {
      // scale: scale,
      startX: 0,
      startY: 0,
      lines: {
        h: [],
        v: []
      },
      shadow: {
        x: 0,
        y: 0,
        width: 0,
        height: 0
      },
      thick: 20,
      width: 500,
      height: 400,
      lang: 'zh-CN', // 
      isShowRuler: true, // 
      isShowReferLine: true, // 
      renderRule: false,
      hoverEle_uuid: null,
      activeEle_uuid: null,
      moveing: false,
      options: {
        handle: '.icon-tuodong',
        animation: 300,
        ghostClass: 'ghostClass',
        chosenClass: 'chosenClass',
        dragClass: 'dragClass'
      },
      showbottomMenuHeight: '',
      modeShow: false
    }
  },
  computed: {
    ...mapState({
      pageWidth: (state) => state.pageWidth,
      zoom: (state) => state.zoom,
      pagedata: (state) => state.pagedata,
      showRuler: (state) => state.showRuler,
      activeEleId: (state) => state.activeEleId,
      preview: (state) => state.preview
    }),
    viewportBoardStyle () {
      return {
        transform: `scale(${this.zoom / 100})`,
        width: `${this.pageWidth}px`,
        height: this.pageHeight == 'auto' ? this.pageHeight : `${this.pageHeight}px`,
        minHeight: this.minHeight == 'auto' ? this.minHeight : `${this.minHeight}px`,
        top: `${this.top}px`,
        left: `${this.left}px`,
        marginBottom: `${this.top}px`
      }
    },
    pageProps () {
      const pageProps = this.pagedata.widgets.find((item) => item.type == 'W0000')
      return pageProps
    },
    // pageBackgroundColor() {
    //   const wpage = this.pagedata.widgets.find((item) => item.type == 'W0000')
    //   return wpage ? wpage.backgroundColor : '#f4f4f4'
    // },
    scale () {
      return this.zoom / 100
    }
  },
  watch: {
    scale: {
      handler (newVal, oldVal) {
        this.renderRule = false
        console.log('.............')
        setTimeout(() => {
          this.renderRule = true
          this.handleScroll()
        }, 20)
      },
      deep: true
    },
    preview: {
      handler (newVal, oldVal) {
        this.modeShow = newVal
      }
    }
  },
  mounted () {
    // EventBus.$on('bus.key.delete', (v) => {
    //   this.$log.debug('[viewport/mIndex.vue] bus.key.delete', this)
    // })
    // EventBus.$on('bus.key.left', (v) => {
    //   this.$log.debug('[viewport/mIndex.vue] bus.key.left', this)
    //   let e = event || window.event
    //   e.preventDefault()
    //   let childInPanel = this.getChildInPanel()
    //   if (childInPanel) {
    //     childInPanel.x--
    //   }
    // })
    // EventBus.$on('bus.key.up', (v) => {
    //   this.$log.debug('[viewport/mIndex.vue] bus.key.up', this)
    //   let e = event || window.event
    //   e.preventDefault()
    //   let childInPanel = this.getChildInPanel()
    //   if (childInPanel) {
    //     childInPanel.y--
    //   }
    // })
    // EventBus.$on('bus.key.right', (v) => {
    //   this.$log.debug('[viewport/mIndex.vue] bus.key.right', this)
    //   let e = event || window.event
    //   e.preventDefault()
    //   let childInPanel = this.getChildInPanel()
    //   if (childInPanel) {
    //     childInPanel.x++
    //   }
    // })
    // EventBus.$on('bus.key.down', (v) => {
    //   this.$log.debug('[viewport/mIndex.vue] bus.key.down', this)
    //   let e = event || window.event
    //   e.preventDefault()
    //   let childInPanel = this.getChildInPanel()
    //   if (childInPanel) {
    //     childInPanel.y++
    //   }
    // })
    // hover
    this.$on('widgetpanel.component.mouseevent', function (uuid) {
      // console.log( '[viewport] widgetpanel.component.mouseevent, ', uuid )
      this.hoverEle_uuid = uuid
    })

    // 
    this.$refs.screensRef.scrollLeft =
      this.$refs.containerRef.getBoundingClientRect().width / 2 - 300 // 300 = #screens.width / 2
    this.$nextTick(() => {
      this.initSize()
    })
  },
  methods: {
    ...mapMutations(['ZOOM_SET', 'SELECT_ELE', 'PREVIEW']),
    handleLine (lines) {
      this.lines = lines
    },
    handleCornerClick () {
      this.ZOOM_SET(100)
    },
    handleScroll () {
      const screensRect = document.querySelector('#viewport-container').getBoundingClientRect()
      const canvasRect = document.querySelector('#draw-container').getBoundingClientRect()
      // 
      const startX = (screensRect.left + this.thick - canvasRect.left) / this.scale
      const startY = (screensRect.top + this.thick - canvasRect.top) / this.scale
      this.startX = startX >> 0
      this.startY = startY >> 0
    },
    handleWheel (e) {
      if (e.ctrlKey || e.metaKey) {
        e.preventDefault()
        const nextScale = parseFloat(Math.max(0.2, this.scale - e.deltaY / 500).toFixed(2))
        this.scale = nextScale
      }
      this.$nextTick(() => {
        this.handleScroll()
      })
    },
    initSize () {
      // const wrapperRect = document.querySelector('#draw-container').getBoundingClientRect()
      const borderWidth = 1
      this.width = this.$el.clientWidth - this.thick - borderWidth
      this.height = this.$el.clientHeight - this.thick - borderWidth
      console.log('000000000000000')
      this.renderRule = true
    },
    handleDraggableOnMove (e) {
      e.preventDefault()
    },

    handleMouseEnter (uuid) {
      if (!this.moveing) {
        this.hoverEle_uuid = uuid
      }
    },
    handleMouseLeave () {
      if (!this.moveing) {
        this.hoverEle_uuid = null
      }
    },
    // 
    handleClickComponent (e, uuid) {
      log.debug('[draw-board] methods handleClickComponent: ', uuid)
      this.SELECT_ELE(uuid)
    },
    // 
    isActive (val) {
      let active = false
      if (val.uuid === this.activeEleId) {
        active = true
      } else {
        if (val.childWidgets) {
          const fd = val.childWidgets.find((item) => item.uuid == this.activeEleId)
          active = !!fd
        }
      }

      return active
    },
    // 
    handleClosePreview (e) {
      if (!e) {
        this.PREVIEW({
          preview: false
        })
      }
    },
    onResizingComponent (widget, left, top, width, height) {
      const { paddingTop, paddingRight, paddingBottom, paddingLeft } = widget
      widget.width = width - paddingRight - paddingLeft
      widget.height = height - paddingTop - paddingBottom
    }
  }
}
</script>
