<template>
  <Modal
    v-model="show"
    title=""
    :loading="loading"
    :mask-closable="false"
    @on-ok="handleConfirm"
    @on-cancel="handleCancel"
  >
    <Form
      ref="form"
      :label-width="100"
      :model="model"
      :rules="rules"
      @submit.native.prevent="handleConfirm"
    >
      <FormItem
        label=""
        prop="tags"
      >
        <TagInput
          v-model="model.tags"
          :list="list"
        />
      </FormItem>
    </Form>
  </Modal>
</template>

<script>
import TagInput from './tag-input'

export default {
  components: { TagInput },

  props: {
    list: Array
  },

  data () {
    return {
      show: false,
      loading: true,
      rules: {
        tags: [{ required: true, type: 'array', message: '' }]
      },
      model: {
        tags: []
      }
    }
  },

  methods: {
    handleConfirm () {
      this.$refs.form.validate((valid) => {
        if (valid) {
          this.show = false
          const { tags } = this.model
          this.$emit('change', [...tags])
          this.$refs.form.resetFields()
        } else {
          this.loading = false
          this.$nextTick(() => {
            this.loading = true
          })
        }
      })
    },
    handleCancel () {
      this.show = false
      this.$refs.form.resetFields()
    }
  }
}
</script>
