import { importAll } from '@/utils'
import req, { createAxios } from './req'

const api = {}

// function importAll (r, fn = (key, r) => r(key)) {
//   r.keys().forEach(key => fn(key, r))
// }

importAll(require.context('./', false, /\.js$/), (key, r) => {
  const keyPath = key.match(/\.\/(.+)\.js$/)[1]
  if (!['index', 'sign'].includes(keyPath)) {
    api[keyPath] = r(key)
  }
})

function install (Vue) {
  Vue.prototype.$api = api
  Vue.prototype.$http = req
}

export default api
export { req, createAxios, install }
