<style lang="scss" src="./index.scss"></style>

<template>
  <div class="sp-widgetspanel">
    <div
      v-for="(nav, index) in navs"
      :key="`navs-${index}`"
      class="nav-item"
    >
      <div class="nav-item__btn">
        {{ nav.name }}
      </div>
      <div
        v-if="nav.children"
        class="sub-nav-con"
      >
        <div
          v-for="(snav, sindex) in nav.children"
          :key="`snavs-${sindex}`"
          class="sub-nav-item"
        >
          <div class="sub-nav-item__btn">
            {{ snav.name }}
          </div>
          <div class="widgets-con">
            <template v-for="widget in snav.widgets">
              <div
                :key="widget.name"
                class="widget-item"
                draggable="true"
                :data-name="widget.name"
                @click="handleAddWidget(widget)"
              >
                <SpIcon
                  class="widget-item__icon"
                  :icon="widget.icon"
                />
                <div class="widget-item__key">
                  {{ widget.key }}
                </div>
                <div class="widget-item__title">
                  {{ widget.title }}
                </div>
              </div>
            </template>
          </div>
        </div>
      </div>
    </div>
    <!-- <SpTab class="sp-widgetspanel__tab" v-for="(item, index) in panel" :key="index">
      <SpTabPanel
        class="sp-widgetspanel__panel"
        v-for="(sitem, sindex) in item"
        :key="sindex"
        :label="sitem"
        :name="sitem"
      >
        <template v-for="(kitem, kindex) in info[sitem]">
          <div
            class="widget-item"
            :draggable="kitem.draggable"
            @click="handleAddWidget(kitem.widget)"
            :data-name="kitem.widget.name"
            :key="kindex"
          >
            <SpIcon class="widget-item__icon" :icon="kitem.widget.icon" />
            <div class="widget-item__title">{{ kitem.widget.title }}</div>
          </div>
        </template>
      </SpTabPanel>
    </SpTab> -->
  </div>
</template>

<script>
import mshopConfig from '@/mshop.config'
import { widgets } from '@/espier-widgets'
import { log } from '@/utils'
import Vue from 'vue'
// import _groupBy from 'lodash/groupBy'
// import _chunk from 'lodash/chunk'
import { mapMutations } from 'vuex'
import axios from 'axios'
export default {
  name: 'SpWidgetsPanel',
  data () {
    return {
      info: null,
      panel: null,
      navs: []
    }
  },
  created () {
    this.registerWidgets()
  },
  mounted () {
    // 
    document.addEventListener('dragstart', function (event) {
      log.debug('[sp-widgetspanel] dragstart, widgets is:', event.target.getAttribute('data-name'))
      event.dataTransfer.setData('Name', event.target.getAttribute('data-name'))
    })

    document.addEventListener('dragover', function (event) {
      event.preventDefault()
    })
  },
  methods: {
    ...mapMutations(['ADD_WIDGET']),
    handleAddWidget (widget) {
      this.ADD_WIDGET(widget)
    },
    async registerWidgets () {
      const { navs } = mshopConfig
      log.debug('[sp-widgetspanel] created, register widgets', navs)

      // const customercomps = ['W1004']
      // for (let i = 0; i < customercomps.length; i++) {
      //   const { data } = await axios.get(
      //     `http://api.test.com/espier.${customercomps[i]}.umd.min.js?d=${new Date().getTime()}`
      //   )

      //   // eslint-disable-next-line no-new-func
      //   new Function(`return ${data}`)()
      //   const regexp = new RegExp(/define\("(espiercmpt.+?)"/gm)
      //   const compname = regexp.exec(data)[1]
      //   console.log(`comname: ${compname}`)
      //   if (window[compname]) {
      //     Vue.component(window[compname].name, window[compname])
      //     Vue.component(window[compname].panel.name, window[compname].panel)
      //     widgets[compname] = window[compname]
      //   }
      // }
      // console.log(widgets)
      navs.forEach((mainNav) => {
        mainNav.children.forEach((subNav, index) => {
          subNav['widgets'] = []
          for (let key in widgets) {
            if (widgets[key].navs[0] == mainNav.id && widgets[key].navs[1] == subNav.id) {
              subNav.widgets.push({
                ...widgets[key],
                key
              })
            }
          }
        })
      })

      console.log(navs)
      this.navs = navs
    }
  }
}
</script>
