<style lang="scss">
.widget-W0018-props {
  .point-style {
    .attr-field__label {
      margin-top: 30px;
    }
  }
  .delete-btn {
    float: right;
    margin-right: 10px;
  }
}
</style>

<template>
  <div class="widget-W0018-props">
    <Tabs value="name1" :animated="false">
      <TabPane
        :label="`${activeElement.name}[${activeElement.type}]`"
        name="name1"
        style="overflow: auto;"
      >
        <panelBar title="">
          <attr-field label="">
            <Input v-model="activeElement.mainTitle" />
          </attr-field>
          <div class="division"></div>
          <attr-field label="">
            <iSwitch size="small" v-model="activeElement.autoplay" />
            <!-- <RadioGroup v-model="activeElement.autoplay">
              <Radio :label="true"></Radio>
              <Radio :label="false"></Radio>
            </RadioGroup> -->
          </attr-field>
          <div class="division"></div>
          <attr-field label="">
            <InputNumber v-model="activeElement.autoplaySpeed" size="small" :min="0"></InputNumber>
          </attr-field>
        </panelBar>
        <panelBar title="">
          <attr-field label="">
            <Select v-model="activeElement.styleType" size="small" label-in-value>
              <Option
                v-for="item in styleList"
                :value="item.value"
                :label="item.label"
                :key="item.value"
              >
                <img :src="item.img" alt width="24" />
                <div>{{ item.label }}</div>
              </Option>
            </Select>
          </attr-field>
          <div class="division"></div>
          <attr-field label="">
            <RadioGroup v-model="activeElement.pointAlign">
              <Radio label="left">
                <span></span>
              </Radio>
              <Radio label="center">
                <span></span>
              </Radio>
              <Radio label="right">
                <span></span>
              </Radio>
            </RadioGroup>
          </attr-field>
          <div class="division"></div>
          <!-- <attr-field label="">
            <color-picker v-model="activeElement.pointDefaultColor"></color-picker>
          </attr-field> -->

          <attr-field label="" labelTop>
            <iSwitch size="small" v-model="activeElement.customerThemeEnabled" />
            <div class="division"></div>
            <color-picker v-model="activeElement.pointActiveColor"></color-picker>
          </attr-field>

          <div class="division"></div>
          <attr-field label="">
            <color-picker v-model="activeElement.pointDefaultColor"></color-picker>
          </attr-field>
        </panelBar>
        <panelBar title="">
          <attr-field label="">
            <InputNumber v-model="activeElement.wgtTitleFontSize" size="small" :min="0" :max="27"></InputNumber>
          </attr-field>
          <div class="division"></div>
          <attr-field label="">
            <color-picker v-model="activeElement.wgtTitleColor"></color-picker>
          </attr-field>
          <div class="division"></div>
          <attr-field label="">
            <InputNumber v-model="activeElement.wgtPriceFontSize" size="small" :min="0"></InputNumber>
          </attr-field>
          <div class="division"></div>
          <attr-field label="">
            <InputNumber v-model="activeElement.wgtToFixed" size="small" :min="0" :max ="2"></InputNumber>
          </attr-field>
          <div class="division"></div>
          <attr-field label="">
            <color-picker v-model="activeElement.wgtPriceColor"></color-picker>
          </attr-field>
        </panelBar>
        <panelBar title="">
          <Button type="primary" @click="handleAdd" style="width:100%;margin:0 auto;"
            ></Button>
          <div v-for="(item, i) in activeElement.data" :key="i" style="margin-top:10px;">
            <Collapse>
              <Panel :name="i.toString()">
                {{ Number(i) + 1 }}
                <span class="delete-btn">
                  <Icon type="ios-trash-outline" size="16" @click.stop="handleDelete(i)" />
                </span>
                <div slot="content">
                  <!-- {{item.pathData}} -->
                 <div class="division"></div>
                 <attr-field label="">
                   <dataBind
                     :value="item.data"
                     :imgPicker="false"
                     :filterMenu="['goods']"
                     :multiple="true"
                     :customPath="false"
                     @pathOnChange="(data) => onChangePath(data, i)"
                     activeKey="goods_id"
                     placeholder=""
                   ></dataBind>
                 </attr-field>
                </div>
              </Panel>
            </Collapse>
          </div>
        </panelBar>
      </TabPane>
      <TabPane label="" name="name2">
        <basic-set v-model="activeElement"></basic-set>
      </TabPane>
    </Tabs>
  </div>
</template>
<script>
import { panelBar, attrField, colorPicker, dataBind } from '../../common/comps'
import basicSet from '../../common/basic-set'
export default {
  name: 'W0018Props',
  props: ['activeElement'],
  data() {
    return {
      data:{},
      test: '',
      styleList: [
        { img: require('./img/11.png'), label: '', value: 'style1' },
        { img: require('./img/22.png'), label: '', value: 'style2' },
        { img: require('./img/33.png'), label: '', value: 'style3' },
        { img: require('./img/44.png'), label: '', value: 'style4' },
        { img: require('./img/55.png'), label: '', value: 'style5' }
      ]
    }
  },
  components: {
    panelBar,
    attrField,
    colorPicker,
    basicSet,
    dataBind
  },
  methods: {
    change() {},
    handleAdd() {
      console.log('this.activeElement', this.activeElement)
      if (this.activeElement.data.length < 5) {
        this.activeElement.data.push({
          data: [
            {goods_id : '', goods_pic : '', price : 1231, market_price: 3333, goods_name : '1',brief :"", tagList : [], linkPage : 'goods', linkPageLabel : '' },
            {goods_id : '', goods_pic : '', price : 4234, market_price: 3333, goods_name : '2',brief :"", tagList : [], linkPage : 'goods', linkPageLabel : '' },
            {goods_id : '', goods_pic : '', price : 68.9, market_price: 3333, goods_name : '3',brief :"", tagList : [], linkPage : 'goods', linkPageLabel : '' },
            {goods_id : '', goods_pic : '', price : 220, market_price: 3333, goods_name : '4',brief :"", tagList : [], linkPage : 'goods', linkPageLabel : '', }
          ]
        })
      } else {
        this.$Message.error('5')
      }
    },
    handleDelete(i) {
      this.$Modal.confirm({
        title: '',
        content: '',
        onOk: () => {
          if (this.activeElement.data.length > 1) {
            this.activeElement.data.splice(i, 1)
            this.$Message.success('')
          } else {
            this.$Message.error('1')
          }
        }
      })
    },
    handleImgOnChange(idx, item) {
      this.activeElement.data[idx].imgUrl = item
    },
    handlePathOnChange(idx, item) {
      this.activeElement.data[idx].pathData = item
    },
    onChangePath(data, index) {
      const goodsData = data.map(item => {
        const { goods_id, pics, price, item_name, linkPage, linkPageLabel ,title,brief,market_price} = item
        return {
          goods_id,
          goods_pic: pics.length > 0 ? pics[0] : '',
          price: price,
          goods_name: item_name,
          linkPage,
          title,
          brief,
          market_price,
          linkPageLabel
        }
      })
      this.activeElement.data[index].data = goodsData
    }
  }
}
</script>
