<template>
  <div class="sp-picker">
    <Picker
      :value="value"
      :is-show="isShow"
      v-bind="$attrs"
      @input="(val) => $emit('input', val)"
    />
    <div
      class="sp-picker__cont"
      @click="isShow = !isShow"
    >
      <slot>{{ value }}</slot>
    </div>
  </div>
</template>

<script>
import Picker from './main'

export default {
  name: 'SpPicker',

  components: { Picker },

  props: {
    value: Object
  },

  data () {
    return {
      isLoaded: false,
      isShow: false
    }
  },

  watch: {
    isShow (val) {
      if (!this.isLoaded && val) {
        this.isLoaded = true
      }
    }
  }
}
</script>
