<template>
  <div class="sp-tag-picker">
    <div class="sp-tag-picker__toolbar">
      <Form
        class="sp-tag-picker__search"
        @submit.native.prevent="handleSubmit"
      >
        <Input
          v-model="query.keywords"
          placeholder=""
        />
        <Button
          type="primary"
          html-type="submit"
        >
          
        </Button>
        <Button
          type="text"
          @click="handleResetQuery"
        >
          
        </Button>
      </Form>
    </div>

    <div class="sp-tag-picker__cont">
      <Table
        :columns="columns"
        :data="list"
        @on-row-click="handleClickItem"
      >
        <template
          slot="selection"
          slot-scope="{ row, index }"
        >
          <Radio :value="curId === row.tag_id" />
        </template>
      </Table>
      <Page
        class="sp-picker-page"
        size="small"
        show-total
        :total="pageCount"
        :current="pageCur"
        :page-size="pageSize"
        @on-change="goPage"
      />
    </div>
  </div>
</template>

<script>
import BasePicker from './base'
import PageMixin from '../mixins/page'
import { resolveQuery } from '../helper'

const defaultQuery = () => ({
  keywords: ''
})

export default {
  extends: BasePicker,

  config: {
    type: 'goods-tag'
  },

  mixins: [PageMixin],

  data () {
    const columns = [
      {
        slot: 'selection'
      },
      {
        title: '',
        key: 'name'
      },
      {
        title: '',
        key: 'num'
      },
      {
        title: '',
        key: 'updated_at'
      }
    ]

    return {
      curId: this.resolveCurId(),
      list: [],
      columns,
      query: defaultQuery()
    }
  },

  created () {
    this.refresh()
  },

  methods: {
    async fetch (params) {
      this.loading = true
      params = {
        ...params,
        q: resolveQuery(this.query)
      }

      try {
        const { count, list } = await this.$api.picker.itemTag(params)
        this.list = list
        this.loading = false
        return {
          count
        }
      } catch (e) {
        this.loading = false
      }
    },
    resolveCurId () {
      if (this.value) {
        const {
          data: { tag_id }
        } = this.value
        return tag_id
      }

      return null
    },
    handleSubmit () {
      this.refresh(true)
    },
    handleResetQuery () {
      this.query = defaultQuery()
    },
    handleClickItem (item, idx) {
      this.curId = item.tag_id
      this.updateVal(item)
    }
  }
}
</script>

<style lang="scss">
.sp-tag-picker {
  &__toolbar {
    padding: 10px 0;
  }
  &__search {
    width: 70%;
    display: flex;
    align-items: center;
    .ivu-select,
    .ivu-input-wrapper,
    .ivu-btn {
      margin-right: 10px;
    }
    .ivu-btn {
      width: 120px;
    }
  }
}
</style>
