<template>
  <div class="sp-category-picker">
    <Form :label-width="80">
      <FormItem label="">
        <Cascader
          placeholder=""
          change-on-select
          :data="category"
          :value="curCat"
          @on-change="handleChange"
        />
      </FormItem>
    </Form>
  </div>
</template>

<script>
import BasePicker from './base'

function resolveCategory (data) {
  const walk = (node) => {
    const { level, child_count, cat_id: value, cat_name: label } = node
    const ret = {
      level,
      value,
      label
    }

    if (child_count > 0) {
      const children = node[`lv${+level + 1}`].map(walk)
      ret.children = children
    }

    return ret
  }

  const category = data.map(walk)
  return category
}

export default {
  extends: BasePicker,

  config: {
    title: ''
  },

  data () {
    const curCat = this.value && this.value.data ? this.value.data.map(({ cat_id }) => cat_id) : []

    return {
      type: 'category',
      category: [],
      defaultVal: [],
      curCat
    }
  },

  created () {
    this.fetch()
  },

  methods: {
    async fetch () {
      const data = await this.$api.picker.itemCategory()
      this.category = resolveCategory(data)
    },
    handleChange (val, selectedData) {
      const data = selectedData.map((v) => {
        return {
          cat_id: v.value,
          cat_name: v.label
        }
      })
      this.updateVal(data)
    }
  }
}
</script>

<style lang="scss">
.category-picker {
}
</style>
