<template>
  <div class="sp-img-picker">
    <Tabs
      :value="curTab"
      :animated="false"
    >
      <TabPane
        label=""
        name="wechat-img"
      >
        <ImgPage
          type="weixin"
          :data="wechatImg"
          :on-change="handleOnChange"
          :fetch="fetchWeixinImg"
          :multiple="multiple"
        />
      </TabPane>
      <TabPane
        label=""
        name="espier-img"
      >
        <ImgPage
          type="local"
          :data="espierImg"
          :on-change="handleOnChange"
          :fetch="fetchLocalImg"
          :multiple="multiple"
        />
      </TabPane>
    </Tabs>
  </div>
</template>

<script>
import PageMixin from '../mixins/page'
import BasePicker from './base'
import Img from '../comps/img'
import TagModal from '../comps/tag-modal'
import ImgUploadModal from '../comps/img-upload-modal'
import ImgEditList from '../comps/img-edit-list'
import ImgPage from '../comps/img-page'
// import { resolveQuery, resolveImgTitle, updateItem, remove } from '../helper'

export default {

  components: { Img, TagModal, ImgUploadModal, ImgEditList, ImgPage },
  extends: BasePicker,

  mixins: [PageMixin],

  config: {
    // title: '',
    type: 'img'
  },

  props: {
    multiple: Boolean
  },

  data () {
    const selection = !this.localVal
      ? []
      : Array.isArray(this.localVal.data)
        ? this.localVal.data
        : [this.localVal.data]
    const { type = 'wechat-img' } = this.value || {}

    return {
      curTab: 'wechat-img',
      wechatImg: [],
      espierImg: [],

      defaultVal: this.multiple ? [] : {},
      selection
    }
  },

  computed: {},

  created () {},

  methods: {
    async fetchWeixinImg ({ page_no, page_size }) {
      const { item, total_count } = await this.$api.picker.wechatImage({
        type: 'image',
        page: page_no,
        pageSize: page_size
      })
      if(item){
        this.wechatImg = item.map((item) => {
          return {
            id: item.media_id,
            name: item.name,
            url: item.url
          }
        })
      }
      return { count: total_count }
    },
    async fetchLocalImg ({ page_no, page_size }) {
      const { list, total_count } = await this.$api.picker.espierImage({
        type: 'image',
        page: page_no,
        pageSize: page_size
      })
      this.espierImg = list.map((item) => {
        return {
          id: item.image_id,
          name: item.image_name,
          url: item.url
        }
      })
      return { count: total_count }
    },
    handleOnChange (data) {
      this.updateVal(data)
    }
  }
}
</script>

<style lang="scss">
.sp-img-picker {
  &-cont {
    display: flex;
    flex-wrap: wrap;
    padding: 20px 6px;
    .img-item {
      width: 118px;
      margin: 0 6px 20px;
      .img-item__inner {
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
        overflow: hidden;
        background: #f8f8f8;
        height: 118px;
        img {
          width: 100%;
          max-height: 118px;
        }
      }
      .img-item__title {
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        height: 30px;
        line-height: 30px;
      }
    }
  }
}
</style>
