<script>
const IMG_TYPES = {
  large: '_l',
  medium: '_m',
  small: '_s',
  thumbnail: '_t'
}

const resolveExt = (file) => {
  const pairs = file.split('.')
  return pairs[pairs.length - 1]
}

export default {
  functional: true,
  props: {
    src: String,
    type: {
      type: String,
      default: 'thumbnail' // large | medium | small | thumbnail
    }
  },
  render (h, context) {
    const { src, type } = context.props
    const rSrc = src ? `${src}${IMG_TYPES[type]}.${resolveExt(src)}` : src

    return <img class='picker-img' src={rSrc} />
  }
}
</script>
