import Main from './main'
import usePickerApi from './api'

/** 
 * this.$picker('goods')
 * await this.$picker('goods-tag', { value: xxx })
 */
export default {
  install (Vue, options = {}) {
    const Ctor = Vue.extend(Main)
    function $picker (type, data) {
      if (!type) {
        throw new Error('type should not be undefined')
      }

      const { data: dataVal, ...rest } = data
      data = {
        ...options,
        value: {
          type,
          data: dataVal
        },
        isShow: true,
        extra: rest
      }

      return new Promise((resolve) => {
        const vm = new Ctor({
          propsData: data,
          created () {
            const teardown = () => {
              this.$destroy()
              this.$el.remove()
            }
            this.$once('close', teardown)
            this.$once('input', (val) => {
              resolve(val)
              teardown()
            })
          }
        }).$mount()
        document.body.appendChild(vm.$el)
      })
    }

    Vue.prototype.$picker = $picker
    Vue.prototype.$api.picker = usePickerApi(Vue.prototype.$http)
  }
}
