<style lang="scss">
.sp-path-bring-picker {
  .picker-hd {
    display: flex;
    margin-bottom: 10px;
  }
  .shop-picker-con {
    flex: 1;
    padding-right: 10px;
  }
  .search-con {
    flex: 1;
  }
}
</style>
<template>
  <div class="sp-path-bring-picker">
    <div class="picker-hd">
      <div class="shop-picker-con">
        <ShopPicker @on-change="handleShopOnChange" />
      </div>
      <div class="search-con">
        <Input
          v-model="keywords"
          suffix="ios-search"
          size="large"
          search
          placeholder=""
          @on-search="handleSearchOnChange"
        />
      </div>
    </div>
    <Table
      :columns="columns"
      height="450"
      :data="list"
      @on-selection-change="handleClickItem"
      @on-row-click="handleClickItem"
    >
      <template
        slot="selection"
        slot-scope="{ row, index }"
      >
        <Radio :value="handleIsSelect(row, index)" />
      </template>
    </Table>

    <Page
      class="sp-picker-page"
      size="small"
      show-total
      :total="pageCount"
      :current="pageCur"
      :page-size="pageSize"
      @on-change="goPage"
    />
  </div>
</template>

<script>
import PageMixin from '../mixins/page'
import ShopPicker from '../comps/shop-picker'
export default {
  name: '',
  components: { ShopPicker },
  mixins: [PageMixin],
  props: {
    onSelect: Function,
    data: [Object, Array, String, Number, Boolean],
    type: String
  },
  data () {
    const columns = [
      {
        slot: 'selection',
        width: '80'
      },
      {
        title: 'ID',
        key: 'article_id',
        width: '100'
      },
      {
        title: '',
        key: 'title',
        width: '480'
      }
    ]

    return {
      columns,
      curId: '',
      pageSize: 10,
      list: [],
      shop: null,
      keywords: '',
      selection: []
    }
  },
  created () {},
  mounted () {
    this.nextPage()
    if (this.$attrs.multiple) {
      this.selection = this.data
    } else {
      this.curId = this.data.article_id
    }
    console.log('path-bring-picker init data: ', this.data)
  },
  methods: {
    async fetch ({ page_no, page_size }) {
      const { list, total_count } = await this.$api.picker.getArticleList({
        pageSize: page_size,
        page: page_no,
        article_type: this.type,
        title: this.keywords,
        distributor_id: this.shop ? this.shop.distributor_id : ''
      })
      this.list = list
      return { count: total_count }
    },
    handleSearchOnChange () {
      this.refresh(true)
    },
    handleClickItem (item) {
      // console.log('pickerItem', item)
      if (this.$attrs.multiple) {
        const findex = this.selection.findIndex(se => se.article_id == item.article_id)
        if (findex > -1) {
          this.selection.splice(findex, 1)
        } else {
          this.selection.push(item)
        }
        // console.log('this.selection', this.selection)
      } else {
        this.curId = item.article_id
        this.selection = item
      }
      this.onSelect && this.onSelect(this.selection)
    },

    // 
    handleShopOnChange (item) {
      this.shop = item
      this.refresh(true)
    },

    //
    handleIsSelect (item, index) {
      if (this.$attrs.multiple) {
        const findex = this.selection.findIndex(se => se.article_id == item.article_id)
        return findex > -1
      } else {
        return this.curId == item.article_id
      }
    }
  }
}
</script>
