<style lang="scss">
.sp-img-picker {
  &-cont {
    display: flex;
    flex-wrap: wrap;
    padding: 10px 6px;
    .img-item {
      width: 118px;
      margin: 0 6px 20px;
      &.is-active {
        .img-item__inner {
          position: relative;
          &::before {
            content: '\E634';
            font-family: 'mshopdesign';
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            background: rgba(0, 0, 0, 0.5);
            color: #fff;
            font-size: 40px;
            display: flex;
            align-items: center;
            justify-content: center;
          }
        }
      }
      .img-item__inner {
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
        overflow: hidden;
        background: #f8f8f8;
        height: 118px;
        img {
          width: 100%;
          max-height: 118px;
        }
      }
      .img-item__title {
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        height: 30px;
        line-height: 30px;
      }
    }
  }
  .sp-img-uploader {
    &__inner {
      padding: 10px 12px 0;
    }
  }
}
</style>

<template>
  <div class="picker-img-page">
    <Upload
      ref="upload"
      class="sp-img-uploader"
      accept="image/*"
      multiple
      :action="action"
      :headers="headers"
      :max-size="2048"
      :data="tdata"
      :format="['jpg', 'jpeg', 'gif', 'png']"
      :show-upload-list="false"
      :before-upload="handleBeforeUpload"
      :on-success="handleSuccess"
      :on-error="handleError"
    >
      <div class="sp-img-uploader__inner">
        <Button type="primary">
          {{ uploadLoading ? '' : `${type == 'weixin' ? '' : ''}` }}
        </Button>
      </div>
    </Upload>
    <div class="sp-img-picker-cont">
      <div
        v-for="item in data"
        :key="item.img_id"
        class="img-item"
        :class="{ 'is-active': handleIsActive(item) }"
        @click="handleClickItem(item)"
      >
        <div class="img-item__inner">
          <img :src="item.url">
        </div>
        <div class="img-item__title">
          {{ item.name }}
        </div>
      </div>
    </div>
    <Page
      class="sp-picker-page"
      size="small"
      show-total
      :total="pageCount"
      :current="pageCur"
      :page-size="pageSize"
      @on-change="goPage"
    />
  </div>
</template>

<script>
import PageMixin from '../mixins/page'
import S from '@/spx'
import UploadUtil from '@/utils/upload'
export default {
  mixins: [PageMixin],

  props: {
    type: String,
    multiple: Boolean,
    value: Array,
    data: Array,
    fetch: Function,
    onChange: Function
  },

  data () {
    let action, _data
    if (this.type == 'weixin') {
      action = `${
        window.__POWERED_BY_QIANKUN__ ? S['SYS_CONFIG'].baseUrl : process.env.VUE_APP_API_BASE_URL
      }/wechat/material`
      _data = {
        isUploadFile: true,
        type: 'image'
      }
    } else {
      action = ``
      _data = {}
    }

    return {
      selection: this.multiple ? [] : {},
      uploadLoading: false,
      action,
      headers: {
        'Authorization': `bearer ${
          window.__POWERED_BY_QIANKUN__ ? S['SYS_CONFIG'].token : process.env.VUE_APP_TOKEN
        }`
      },
      tdata: _data
    }
  },

  created () {},

  mounted () {
    this.nextPage()
  },

  methods: {
    handleClickItem (item) {
      if (this.multiple) {
        const findex = this.selection.findIndex((se) => se.id == item.id)
        if (findex > -1) {
          this.selection.splice(findex, 1)
        } else {
          this.selection.push(item)
        }
      } else {
        if (this.selection) {
          this.selection = this.selection.id == item.id ? {} : item
        } else {
          this.selection = item
        }
      }
      this.onChange(this.selection)
    },

    handleIsActive (item) {
      const { multiple, selection } = this
      if (multiple) {
        const findex = selection.findIndex((se) => se.id == item.id)
        if (findex > -1) {
          return true
        } else {
          return false
        }
      } else {
        return selection.id == item.id
      }
    },

    handleBeforeUpload (file) {
      if (this.type == 'local') {
        const upload = new UploadUtil()
        // 
        upload
          .uploadImg(file)
          .then(async (res) => {
            await this.$api.picker.uploadLocalPic({
              image_cat_id: 2, // ,
              image_name: file.name, // ,50
              image_url: res.key, // 
              image_type: file.type, // 20
              storage: 'image' // id
            })
            this.refresh(true)
            this.$Message.success('')
          })
          .catch((e) => {
            this.$Message.error('')
          })
        return false
      } else {
        return true
      }
    },

    handleSuccess (response, file, fileList) {
      this.refresh(true)
      this.$Message.success('')
    },
    handleError (error) {
      this.$Message.error('')
      console.log(error)
    }
  }
}
</script>
