<template>
  <div class="sp-path-picker">
    <div class="picker-slider__left">
      <Menu
        :active-name="activeMenu"
        width="120px"
        @on-select="menuOnSelect"
      >
        <MenuItem
          v-for="(nav, idx) in navs"
          :key="`picker-nav__${idx}`"
          :disabled="isDisabled(nav.id)"
          :name="nav.id"
        >
          {{ nav.label }}
        </MenuItem>
      </Menu>
    </div>
    <div class="picker-slider__right">
      <!-- {{$attrs}} -->
      <!-- {{value.data}} -->
      <PathGoodsPicker
        v-if="activeMenu == 'goods'"
        v-bind="this.$attrs"
        :data="value.data"
        :on-select="handleOnChange"
      />

      <PathCategoryPicker
        v-if="activeMenu == 'category'"
        v-bind="this.$attrs"
        :data="value.data"
        :on-select="handleOnChange"
      />

      <PathMainCategoryPicker
        v-if="activeMenu == 'maincategory'"
        v-bind="this.$attrs"
        :data="value.data"
        :on-select="handleOnChange"
      />

      <PathBrandPicker
        v-if="activeMenu == 'brand'"
        v-bind="this.$attrs"
        :data="value.data"
        :on-select="handleOnChange"
      />

      <PathTagPicker
        v-if="activeMenu == 'tag'"
        v-bind="this.$attrs"
        :data="value.data"
        :on-select="handleOnChange"
      />

      <PathManagementPicker
        v-if="activeMenu == 'management'"
        type="general"
        v-bind="this.$attrs"
        :data="value.data"
        :on-select="handleOnChange"
      />

      <PathBringPicker
        v-if="activeMenu == 'bring'"
        type="bring"
        v-bind="this.$attrs"
        :data="value.data"
        :on-select="handleOnChange"
      />

      <PathPagePicker
        v-if="activeMenu == 'page'"
        v-bind="this.$attrs"
        :data="value.data"
        :on-select="handleOnChange"
      />

      <PathMarketPicker
        v-if="activeMenu == 'marketing'"
        v-bind="this.$attrs"
        :data="value.data"
        :on-select="handleOnChange"
      />

      <PathSeckillPicker
        v-if="activeMenu == 'seckill'"
        v-bind="this.$attrs"
        :data="value.data"
        :on-select="handleOnChange"
      />

      <PathCustomerPicker
        v-if="activeMenu == 'customer'"
        v-bind="this.$attrs"
        :data="value.data"
        :on-select="handleOnChange"
      />

      <PathLivePicker
        v-if="activeMenu == 'live'"
        v-bind="this.$attrs"
        :data="value.data"
        :on-select="handleOnChange"
      />
      <PathCouponsPicker
        v-if="activeMenu == 'coupons'"
        v-bind="this.$attrs"
        :data="value.data"
        :on-select="handleOnChange"
      />
    </div>
  </div>
</template>

<script>
import PageMixin from '../mixins/page'
import BasePicker from './base'
import PathGoodsPicker from './path-goods-picker'
import PathCategoryPicker from './path-category-picker'
import PathMainCategoryPicker from './path-maincategory-picker'
import PathBrandPicker from './path-brand-picker'
import PathTagPicker from './path-tag-picker'
import PathManagementPicker from './path-management-picker'
import PathBringPicker from './path-bring-picker'
import PathPagePicker from './path-page-picker'
import PathMarketPicker from './path-market-picker'
import PathSeckillPicker from './path-seckill-picker'
import PathCustomerPicker from './path-customer-picker'
import PathLivePicker from './path-live-picker'
import PathCouponsPicker from './path-coupons-picker'
// import { resolveQuery, resolveImgTitle, updateItem } from '../helper'

const reslovelinkhref = (item) => {
  let linkPath = ''
  if (!item) {
    return linkPath
  }

  switch (item.linkPage) {
    case 'goods':
      linkPath = `/items/${item.itemId}`
      break
    case 'page':
      linkPath = item.url
      break
    case 'category':
      linkPath = `/items?cat_id=${item.category_id}`
      break
    case 'maincategory':
      linkPath = `/items?main_category=${item.category_id}`
      break
    case 'brand':
      linkPath = `/items?brand_id=${item.attribute_id}`
      break
    case 'customer':
      linkPath = `/custompage?pageid=${item.theme_pc_template_id}`
      break
    case 'href':
      linkPath = item.href
      break
    case 'coupons':
      linkPath = item.href
      break
  }
  return linkPath
}

export default {
  components: {
    PathGoodsPicker,
    PathCategoryPicker,
    PathMainCategoryPicker,
    PathBrandPicker,
    PathTagPicker,
    PathManagementPicker,
    PathBringPicker,
    PathPagePicker,
    PathMarketPicker,
    PathSeckillPicker,
    PathCustomerPicker,
    PathLivePicker,
    PathCouponsPicker
  },
  extends: BasePicker,
  mixins: [PageMixin],
  config: {
    title: '',
    type: 'path'
  },
  props: {
    // multiple: Boolean,
    // filterMenu: Array
  },
  data () {
    let activeMenu = ''
    if (this.$attrs.multiple) {
      if (this.value.data.length > 0) {
        activeMenu = this.value.data[0].linkPage
      } else {
        activeMenu = this.$attrs.filterMenu ? this.$attrs.filterMenu[0] : 'goods'
      }
    } else {
      if (Object.keys(this.value.data).length > 0) {
        activeMenu = this.value.data.linkPage
      } else {
        activeMenu = this.$attrs.filterMenu ? this.$attrs.filterMenu[0] : 'goods'
      }
    }

    return {
      navs: [
        { id: 'goods', label: '' },
        { id: 'category', label: '' },
        { id: 'maincategory', label: '' },
        { id: 'brand', label: '' },
        // { id: 'tag', label: '' },
        // { id: 'management', label: '' },
        // { id: 'bring', label: '' },
        { id: 'page', label: '' },
        // { id: 'marketing', label: '' },
        // { id: 'activity', label: '' },
        // { id: 'seckill', label: '' },
        { id: 'customer', label: '' },
        // { id: 'live', label: '' }
        { id: 'coupons', label: '' }
      ],
      activeMenu
    }
  },

  computed: {},

  created () {},

  methods: {
    isDisabled (id) {
      const { filterMenu } = this.$attrs
      return !filterMenu ? false : !(filterMenu.indexOf(id) > -1)
    },
    menuOnSelect (name) {
      this.activeMenu = name
    },
    handleOnChange (data) {
      const result = this.navs.find((nav) => nav.id == this.activeMenu)
      const { activeKey } = this.$attrs
      let fd
      if (Array.isArray(data)) {
        fd = data.filter(item => !!item[activeKey])
        fd = fd.map(item => {
          return {
            ...item,
            linkPage: result.id,
            linkPageLabel: result.label,
            href: reslovelinkhref({
              linkPage: result.id,
              ...item
            })
          }
        })
      } else {
        fd = {
          ...data,
          linkPage: result.id,
          linkPageLabel: result.label,
          href: reslovelinkhref({
            linkPage: result.id,
            ...data
          })
        }
      }
      console.log('path picker fd:', fd)
      this.updateVal(fd)
    }
  }
}
</script>

<style lang="scss">
.sp-path-picker {
  display: flex;
  .picker-slider {
    &__left {
      margin-right: 16px;
    }
  }
  .ivu-menu-item-disabled {
    color: $color-disabled-text;
    &:hover {
      color: $color-disabled-text !important;
    }
  }
}
</style>
