<style lang="scss">
.sp-attr-panel {
  height: 100%;
  .ivu-tabs-bar {
    margin-bottom: 0 !important;
  }
  .ivu-input-number,
  .ivu-select {
    width: 100px;
  }
  .ivu-tabs-content {
    position: absolute;
    top: 40px;
    bottom: 0;
    left: 0;
    right: 0;
  }
  .ivu-tabs-tabpane {
    overflow: auto;
    height: 100%;
  }
  > div {
    height: inherit;
    .ivu-tabs {
      height: inherit;
    }
  }
}
</style>

<template>
  <div class="sp-attr-panel">
    <template v-if="getActiveWidget && hackReset">
      <component
        :is="`${getActiveWidget.type}Props`"
        v-model="getActiveWidget"
        :active-element="getActiveWidget"
      />
    </template>
  </div>
</template>
<script>
// import { log } from '@/utils'
import { mapGetters, mapState } from 'vuex'
export default {
  name: 'SpStylePanel',
  //   components: {
  //     mNavStyle,
  //     mPheaderStyle
  //   },
  props: {},

  data () {
    return {
      hackReset: false
    }
  },
  computed: {
    ...mapState({
      activeEleId: (state) => state.activeEleId,
      pagedata: (state) => state.pagedata
    }),
    ...mapGetters(['getActiveWidget'])
  },
  watch: {
    activeEleId (newVal, oldVal) {
      this.hackReset = false
      this.$nextTick(() => {
        console.log('', this.getActiveWidget)
        this.hackReset = true // 
      })
    }
    // activeElement (newVal, oldVal) {
    //   if (newVal.uuid !== oldVal.uuid) {
    //     this.$log.debug('[panel] mindex activeElement change')
    //     this.hackReset = false
    //     this.$nextTick(() => {
    //       this.hackReset = true // 
    //     })
    //   }
    // }
  },
  created () {
    // this.widgets = plugins
  },
  mounted () {},
  methods: {}
}
</script>
