<style lang="scss" src="./index.scss"></style>

<template>
  <div class="sp-tabs">
    <div class="sp-tabs-header">
      <div
        v-for="(item, index) in navList"
        :key="index"
        class="sp-tabs-header-item"
        :class="{ active: activeKey == item.name }"
        @click="handleChange(item)"
      >
        <SpIcon
          v-if="item.icon"
          :icon="item.icon"
        />
        <span>{{ item.label }}</span>
      </div>
    </div>
    <div
      ref="panes"
      class="sp-tabs-content"
    >
      <slot />
    </div>
  </div>
</template>

<script>
export default {
  name: 'SpTab',
  props: {
    value: {
      type: [String, Number]
    }
  },
  data () {
    return {
      navList: [],
      activeKey: null
    }
  },
  provide () {
    return { tab: this }
  },
  created () {},
  mounted () {
    this.activeKey = this.value ? this.value : this.navList[0].name
    this.updateTab()
  },
  methods: {
    handleChange (item) {
      this.activeKey = item.name
      this.updateTab()
    },
    updateTab () {
      this.navList.forEach((item) => {
        item.show = item.name === this.activeKey
      })
    }
  }
}
</script>
