<style lang="scss">
.sp-about {
}
</style>
<template>
  <div class="sp-about">
    <span
      class="menu-item"
      @click="handleClick"
    >
      <div class="menu-item__icon">
        <SpIcon :icon="icon" />
      </div>
      <span class="menu-item__name">{{ text }}</span>
    </span>
    <Modal
      v-model="showModal"
      draggable
      title=""
      footer-hide
    >
      <div>
        <div>{{ designVersion }}</div>
        <!-- <div>{{ widgetsVersion }}</div> -->
      </div>
    </Modal>
  </div>
</template>

<script>
import pkg from '../../../package.json'
// import pkgWidget from '../../espier-widgets/package.json'
export default {
  name: 'SpAbout',
  props: {
    icon: String,
    text: String
  },
  data () {
    return {
      showModal: false,
      designVersion: '',
      widgetsVersion: ''
    }
  },
  methods: {
    handleClick () {
      this.designVersion = pkg.version
      // this.widgetsVersion = pkgWidget.version
      // console.log(pkgWidget)
      this.showModal = true
    }
  }
}
</script>
