<style lang="scss">
  .shop-picker {
    .modal-bd {

      // padding: 10px 0;
      .store-wrap {
        height: 400px;
        overflow-y: auto;
      }

      .store-item {
        // margin-bottom: 10px;
        cursor: pointer;
        padding: 10px;
        display: flex;
        align-items: center;

        &:hover {
          background: #f0faff;
        }

        .store-name {
          font-size: 14px;
        }
      }
    }
  }
</style>
<template>
  <div class="shop-picker">
    <div class="modal-hd">
      <Row :gutter="16">
        <Input v-model="keywords" suffix="ios-search" placeholder="" search @on-search="handleSearchOnChange" />
      </Row>
    </div>
    <div class="modal-bd">
      <div class="store-wrap">
        <div v-for="(item, idx) in list" :key="`shop-item__${idx}`" class="store-item" @click="handleClickShop(item)">
          <Radio :value="selection ? item.distributor_id == selection.distributor_id : false" />
          <div>
            <div class="store-name">
              {{ item.store_name }}
            </div>
            <div class="store-address">
              {{ item.store_address }}
            </div>
          </div>
        </div>
      </div>
      <Page class="sp-picker-page" size="small" show-total :total="pageCount" :current="pageCur" :page-size="pageSize"
        @on-change="goPage" />
    </div>

  </div>
</template>

<script>
  import regions from '../comps/district.json'
  import PageMixin from '../mixins/page'
  import BasePicker from './base'
  import {
    resolveSelectVal
  } from '../helper'
  export default {
    name: '',
    mixins: [PageMixin],
    extends: BasePicker,
    config: {
      title: '',
      type: 'shop'
    },
    props: {
      // onCustomChange: Function,
      test: String
    },
    data() {
      return {
        loading: true,
        show: false,
        regions: [],
        list: [],
        page_size: 10,
        selection: null,
        address: [],
        keywords: '',
        province: '',
        city: '',
        area: ''
      }
    },
    created() {},
    mounted() {
      this.regions = regions
      this.nextPage()
    },
    methods: {
      async fetch({
        page_no,
        page_size
      }) {
        const {
          list,
          total_count
        } = await this.$api.picker.getDistributors({
          pageSize: page_size,
          page: page_no,
          name: this.keywords,
          province: this.province,
          city: this.city,
          area: this.area,
          is_valid: true
        })
        this.list = list
        return {
          count: total_count
        }
      },
      handleSearchOnChange() {
        this.refresh()
      },
      handleDeleteShop() {
        this.selection = null
        this.handleConfirm()
      },
      handleConfirm() {
        // this.onCustomChange && this.onCustomChange(this.selection)
        this.$emit('on-change', this.selection)
      },
      handleCancel() {},
      handleClickShop(item) {
        this.selection = item
        this.updateVal(item)
      }
    }
  }
</script>
