<template>
  <div class="imgs-list">
    <div
      v-for="item in list"
      :key="item.img_id"
      class="img-item"
      :class="{ 'is-active': selectionDict[item.img_id] }"
      @click="handleClickItem(item)"
    >
      <div class="img-item__inner">
        <Img :src="item.url" />
      </div>
      <div class="img-item__title">
        <Checkbox :value="selectionDict[item.img_id]" />
        {{ item.title }}
      </div>
    </div>
  </div>
</template>

<script>
import Img from './img'

export default {
  components: { Img },

  props: {
    value: Array,
    list: Array
  },

  data () {
    return {
      selection: []
    }
  },

  computed: {
    selectionDict () {
      const key = 'img_id'
      return this.selection.reduce((val, item) => ({ ...val, [item[key]]: true }), {})
    }
  },

  watch: {
    value: {
      immediate: true,
      handler (val) {
        if (!Array.isArray(val)) {
          this.selection = []
        } else {
          this.selection = [...val]
        }
      }
    }
  },

  methods: {
    handleClickItem (item) {
      const idx = this.selection.findIndex((s) => s.img_id === item.img_id)
      if (idx >= 0) {
        this.selection.splice(idx, 1)
      } else {
        this.selection.push(item)
      }
      this.$emit('input', this.selection)
    }
  }
}
</script>

