<style lang="scss">
.sp-path-category-picker {
  // .category-level__1 {
  //   padding-left: 0;
  // }
}
</style>
<template>
  <div class="sp-path-category-picker">
    <Table
      :loading="loading"
      :columns="columns"
      height="520"
      :data="list"
      @on-selection-change="handleClickItem"
      @on-row-click="handleClickItem"
    >
      <template
        slot="selection"
        slot-scope="{ row, index }"
      >
        <Radio :value="handleIsSelect(row, index)" />
      </template>
    </Table>
  </div>
</template>

<script>
const resoveTreeToList = (data, list) => {
  data.forEach((item) => {
    list.push({
      category_id: item.category_id,
      category_name: item.category_name,
      category_level: parseInt(item.category_level),
      hasChild: !!item.children
    })
    if (item.children) {
      resoveTreeToList(item.children, list)
    }
  })
  return list
}

const getCategoryById = (list, id) => {
  let data
  list.map((item) => {
    if (item.category_id == id) {
      data = item
    } else {
      if (item.children) {
        const child = getCategoryById(item.children, id)
        if (child) {
          data = child
        }
      }
    }
  })
  return data
}

export default {
  name: '',
  props: {
    onSelect: Function,
    data: [Object, Array, String, Number, Boolean]
  },
  data () {
    const columns = [
      {
        slot: 'selection',
        width: '80'
      },
      {
        title: 'ID',
        key: 'category_id',
        width: '150',
        render: (h, params) => {
          const { row } = params
          return h(
            'div',
            {
              style: {
                paddingLeft: `${(row.category_level - 1) * 20}px`
              }
            },
            [
              h('Icon', {
                props: {
                  type: row.hasChild ? 'ios-arrow-forward' : ''
                }
              }),
              h('span', row.category_id)
            ]
          )
        }
      },
      {
        title: '',
        key: 'category_name',
        width: '430'
      }
    ]

    return {
      columns,
      curId: '',
      list: [],
      datasource: [],
      selection: [],
      loading: true
    }
  },
  created () {},
  mounted () {
    if (this.$attrs.multiple) {
      this.selection = this.data || []
    } else {
      this.curId = this.data.category_id
    }
    this.fetch()
  },
  methods: {
    async fetch () {
      const res = await this.$api.picker.getCategory()
      this.datasource = res
      // 
      this.list = this.$attrs.multiple ? res : resoveTreeToList(res, [])
      this.loading = false
    },
    handleClickItem (item) {
      if (this.$attrs.multiple) {
        const findex = this.selection.findIndex((se) => se.category_id == item.category_id)
        if (findex > -1) {
          this.selection.splice(findex, 1)
        } else {
          this.selection.push(item)
        }
      } else {
        if (this.curId == item.category_id) {
          this.curId = ''
          this.selection = {}
        } else {
          this.curId = item.category_id
          this.selection = getCategoryById(this.datasource, item.category_id)
        }
      }
      this.onSelect && this.onSelect(this.selection)
    },

    //
    handleIsSelect (item, index) {
      if (this.$attrs.multiple) {
        const findex = this.selection.findIndex((se) => se.category_id == item.category_id)
        return findex > -1
      } else {
        return this.curId == item.category_id
      }
    }
  }
}
</script>
