<style lang="scss">
.shop-picker {
  &__con {
    border: 1px solid $color-border-gray;
    display: flex;
    align-items: center;
    height: 36px;
    border-radius: 4px;
    padding-left: 7px;
    cursor: pointer;
    .shop-info {
      flex: 1;
      line-height: 16px;
      height: 36px;
      margin-right: 20px;
      &__name {
        font-size: 14px;
        margin-top: 4px;
      }
      &__address {
        font-size: 12px;
        color: $color-hint-text;
        @include mult-ellipsis(1);
      }
    }
    .icon-delete1 {
      // float: right;
      // position: relative;
      // right: -24px;
      // top: -24px;
      font-size: 14px;
    }
    .shop-icon {
      width: 36px;
      text-align: center;
      line-height: 34px;
      .icon-wenjianjia {
        color: #b7b6b6;
      }
    }
  }
}
.modal-bd {
  // padding: 10px 0;
  .store-wrap {
    height: 400px;
    overflow-y: auto;
  }
  .store-item {
    // margin-bottom: 10px;
    cursor: pointer;
    padding: 10px;
    display: flex;
    align-items: center;
    &:hover {
      background: #f0faff;
    }
    .store-name {
      font-size: 14px;
    }
  }
}
</style>
<template>
  <div class="shop-picker">
    <div class="shop-picker__con">
      <div
        class="shop-info"
        @click="handleToggleModal"
      >
        <div class="shop-info__name">
          {{ selection ? selection.store_name : '' }}
        </div>
        <div class="shop-info__address">
          {{ selection ? selection.store_address : '' }}
        </div>
      </div>
      <i
        v-if="selection"
        class="mshopdesign icon-delete1"
        @click="handleDeleteShop"
      />
      <div class="shop-icon">
        <i class="mshopdesign icon-wenjianjia" />
      </div>
    </div>
    <Modal
      v-model="show"
      title=""
      :mask-closable="false"
      @on-ok="handleConfirm"
      @on-cancel="handleCancel"
    >
      <div class="modal-hd">
        <Row :gutter="16">
          <Col span="12" />
          <Input
            v-model="keywords"
            suffix="ios-search"
            placeholder=""
            search
            @on-search="handleSearchOnChange"
          />
          </Col>
          <Col span="12" />
          <Cascader
            v-model="address"
            :data="regions"
            @on-change="handleCascaderOnChange"
          />
          </Col>
        </Row>
      </div>
      <div class="modal-bd">
        <div class="store-wrap">
          <div
            v-for="(item, idx) in list"
            :key="`shop-item__${idx}`"
            class="store-item"
            @click="handleClickShop(item)"
          >
            <Radio :value="selection ? item.distributor_id == selection.distributor_id : false" />
            <div>
              <div class="store-name">
                {{ item.store_name }}
              </div>
              <div class="store-address">
                {{ item.store_address }}
              </div>
            </div>
          </div>
        </div>
        <Page
          class="sp-picker-page"
          size="small"
          show-total
          :total="pageCount"
          :current="pageCur"
          :page-size="pageSize"
          @on-change="goPage"
        />
      </div>
    </Modal>
  </div>
</template>

<script>
import regions from './district.json'
import PageMixin from '../mixins/page'
export default {
  name: '',
  mixins: [PageMixin],
  props: {
    // onCustomChange: Function,
    test: String
  },
  data () {
    return {
      loading: true,
      show: false,
      regions: [],
      list: [],
      page_size: 10,
      selection: null,
      address: [],
      keywords: '',
      province: '',
      city: '',
      area: ''
    }
  },
  created () {},
  mounted () {
    this.regions = regions
    this.nextPage()
  },
  methods: {
    async fetch ({ page_no, page_size }) {
      const { list, total_count } = await this.$api.picker.getDistributors({
        pageSize: page_size,
        page: page_no,
        name: this.keywords,
        province: this.province,
        city: this.city,
        area: this.area,
        is_valid: true
      })
      this.list = list
      return { count: total_count }
    },
    handleSearchOnChange () {
      this.refresh()
    },
    handleDeleteShop () {
      console.log('handleDeleteShop')
      this.selection = null
      this.handleConfirm()
    },
    handleCascaderOnChange (value, selectedData) {
      if (selectedData.length == 0) {
        this.province = ''
        this.city = ''
        this.area = ''
      } else {
        this.province = selectedData[0].label
        this.city = selectedData[1].label
        this.area = selectedData[2].label
      }
      this.refresh()
    },
    handleToggleModal () {
      this.show = true
    },
    handleConfirm () {
      // this.onCustomChange && this.onCustomChange(this.selection)
      this.$emit('on-change', this.selection)
    },
    handleCancel () {},
    handleClickShop (item) {
      this.selection = item
    }
  }
}
</script>
