<style lang="scss">
.sp-setup {
  position: relative;
  &__list {
    position: absolute;
    width: 150px;
    background: #fff;
    border: 1px solid $color-border-gray;
    z-index: $z-index-level-10;
    text-align: left;
    li {
      padding: 0 10px;
      background: $color-bg-gray;
      height: 26px;
      line-height: 26px;
      display: flex;
      align-items: center;
      &:hover {
        background: $color-border-gray-light;
      }
    }
  }
  // &__icon {
  //   display: inline-block;
  //   width: 20px;
  // }
}
</style>
<template>
  <div
    v-on-clickaway="handleAway"
    class="sp-setup"
  >
    <span
      class="menu-item"
      @click="handleClick"
    >
      <div class="menu-item__icon">
        <SpIcon :icon="icon" />
      </div>
      <span class="menu-item__name">{{ text }}</span>
    </span>
    <SpDptransition>
      <ul
        v-if="transition"
        class="sp-setup__list"
      >
        <li
          v-for="(item, index) in list"
          :key="index"
          @click="handleListClick(item.key)"
        >
          <span class="sp-setup__icon">
            <SpIcon
              v-if="handleShow(item.status)"
              icon="icon-check"
            />
          </span>
          <span class="sp-setup__name">{{ item.name }}</span>
        </li>
      </ul>
    </SpDptransition>
  </div>
</template>

<script>
import { mixin as clickaway } from 'vue-clickaway'
import { mapState, mapMutations } from 'vuex'
export default {
  name: 'SpSetup',
  mixins: [clickaway],
  props: {
    icon: String,
    text: String
  },
  data () {
    return {
      transition: false,
      list: [
        { status: 'showRuler', name: '', key: 'ruler' },
        { status: 'showLine', name: '', key: 'line' }
      ]
    }
  },
  computed: mapState({
    showRuler: (state) => state.showRuler,
    showLine: (state) => state.showLine
  }),
  methods: {
    ...mapMutations(['SET_RULER', 'SET_LINE']),
    handleShow (status) {
      return this[status]
    },
    handleClick () {
      this.transition = !this.transition
    },
    handleAway () {
      this.transition = false
    },
    handleListClick (key) {
      switch (key) {
        case 'ruler':
          this.SET_RULER()
          break
        case 'line':
          this.SET_LINE()
          break
      }
    }
  }
}
</script>
