<style lang="scss">
.sp-path-goods-picker {
  .picker-hd {
    display: flex;
    margin-bottom: 10px;
  }
  .shop-picker-con {
    flex: 1;
    padding-right: 10px;
  }
  .search-con {
    flex: 1;
  }
}
</style>
<template>
  <div class="sp-path-goods-picker">
    <Table
      :columns="columns"
      height="450"
      :data="list"
      @on-selection-change="handleClickItem"
      @on-row-click="handleClickItem"
    >
      <template
        slot="selection"
        slot-scope="{ row, index }"
      >
        <Radio :value="handleIsSelect(row, index)" />
      </template>
    </Table>

    <Page
      class="sp-picker-page"
      size="small"
      show-total
      :total="pageCount"
      :current="pageCur"
      :page-size="pageSize"
      @on-change="goPage"
    />
  </div>
</template>

<script>
import PageMixin from '../mixins/page'
import ShopPicker from '../comps/shop-picker'
export default {
  name: '',
  components: { ShopPicker },
  mixins: [PageMixin],
  props: {
    onSelect: Function,
    data: [Object, Array, String, Number, Boolean]
  },
  data () {
    const { data } =this.$attrs
        // debugger
    const columns = [
      {
        slot: 'selection',
        width: '80'
      },
      {
        title: '',
        key: 'title',
        width: '480'
      }
    ]

    return {
      columns,
      curId: '',
      pageSize: 10,
      list: [],
      shop: null,
      selection: []
    }
  },
  created () {},
  mounted () {
    this.nextPage()
    console.log(this.$attrs)
    // debugger
    if (this.$attrs.multiple) {
      this.selection = this.data
    } else {
      this.curId = this.data.card_id
    }
    console.log('path-goods-picker init data: ', this.data)
  },
  methods: {
    async fetch ({ page_no, page_size }) {
      const { list, total_count } = await this.$api.picker.getLisDiscountcard({
        page_size: page_size,
        page_no: page_no,
        end_date: 1
      })
      this.list = list
      return { count: total_count }
    },
    handleClickItem (item) {
      // console.log('pickerItem', item)
      if (this.$attrs.multiple) {
        const findex = this.selection.findIndex(se => se.card_id == item.card_id)
        if (findex > -1) {
          this.selection.splice(findex, 1)
        } else {
          this.selection.push(item)
        }
        // console.log('this.selection', this.selection)
      } else {
        this.curId = item.card_id
        this.selection = item
      }
      this.onSelect && this.onSelect(this.selection)
    },

    //
    handleIsSelect (item, index) {
      if (this.$attrs.multiple) {
        const findex = this.selection.findIndex(se => se.card_id == item.card_id)
        return findex > -1
      } else {
        return this.curId == item.card_id
      }
    }
  }
}
</script>
