import Vue from 'vue'
import iView from 'iview'
// import SpxView from '@shopex/spx-view'
import log from '@/utils/log'
import Widgets from './espier-widgets'
import App from './App.vue'
import router from './router'
import store from './store'
import Components from './components'

// import VueAwesomeSwiper from 'vue-awesome-swiper'
import { install as apiInstall } from '@/api'
import S from '@/spx'
import actions from '@/utils/actions'
import draggable from 'vuedraggable'
import VueDraggableResizable from 'vue-draggable-resizable-gorkys'
import 'vue-draggable-resizable-gorkys/dist/VueDraggableResizable.css'

import 'iview/dist/styles/iview.css'
// import './style/iview.less'
import './style/index.scss'

// import 'swiper/dist/css/swiper.css'

apiInstall(Vue)
Vue.use(iView)
// Vue.use(SpxView)
Vue.use(Components)
// Vue.use( Widgets )
Widgets.install(Vue)

Vue.component('vue-draggable-resizable', VueDraggableResizable)
Vue.component('draggable', draggable)
// Vue.use(VueAwesomeSwiper)

Vue.config.productionTip = false
Vue.prototype.$log = log

S.trigger('$vue', Vue)
S.trigger('$router', router)
S.trigger('$store', store)

router.beforeEach((to, from, next) => {
  next()
})

let instance = null
function render (props) {
  try {
    // const { sysConfig } = props
    // S.trigger('SYS_CONFIG', sysConfig)
    // console.log( 'qiankun subapprender', sysConfig )
    actions.setActions(props)
  } catch (e) {

  }
  instance = new Vue({
    router,
    store,
    render: (h) => h(App)
  }).$mount('#app-design')
  window.vm = instance
}
/* eslint-disable */
if (window.__POWERED_BY_QIANKUN__) {
  __webpack_public_path__ = window.__INJECTED_PUBLIC_PATH_BY_QIANKUN__
} else {
  render()
}
/* eslint-disable */
export async function bootstrap( props ) {
  console.log('qiankun subappbootstrap')
}
/* eslint-disable */
export async function mount( props ) {
  console.log( 'qiankun subappmount' )
  store.commit('RESET_PAGEDATA')
  
  render(props)
}
/* eslint-disable */
export async function unmount( props ) {
  console.log('qiankun subappunmount')
  instance.$destroy()
}
