<template>
  <Upload
    ref="upload"
    class="sp-img-uploader"
    accept="image/*"
    action="//jsonplaceholder.typicode.com/posts/"
    :max-size="2048"
    :format="['jpg', 'jpeg', 'gif', 'png']"
    :show-upload-list="false"
    :default-file-list="defaultList"
    :before-upload="handleBeforeUpload"
    :on-success="handleSuccess"
    :on-error="handleError"
  >
    <div class="sp-img-uploader__inner">
      <div
        v-if="!loading && value"
        class="imgs-list"
      >
        <div class="img-file-item">
          <Img :src="value" />
        </div>
      </div>
      <div
        v-else
        class="btn-upload"
      >
        <template v-if="loading">
          <Icon
            type="ios-loading"
            size="32"
          />
          <p></p>
        </template>
        <template v-else>
          <Icon
            type="ios-cloud-upload"
            size="32"
          />
          <p></p>
        </template>
      </div>
    </div>
  </Upload>
</template>

<script>
import Img from './img'

export default {
  components: { Img },

  props: {
    value: [Array, String]
  },

  data () {
    return {
      loading: false,
      defaultList: []
    }
  },

  watch: {
    value: {
      immediate: true,
      handler (val) {
        this.defaultList = val ? [{ url: val }] : []
      }
    }
  },

  methods: {
    reset () {
      this.loading = false
      this.$refs.upload.clearFiles()
    },
    handleSuccess (res, file, fileList) {
      // TODO: demo
      this.loading = false
      file.url =
        'https://onex-pic.oss-cn-shanghai.aliyuncs.com/demo/b2b2c/images/cd/81/21/77061553cc7752f19e61e66a530cd43b290c7ee2.jpeg'
      this.$refs.upload.fileList = [file]
      this.$emit('input', file.url)
    },
    handleError (error, file, fileList) {
      this.$Message.error(error.message || '')
      this.loading = false
    },
    handleBeforeUpload () {
      this.loading = true
    }
  }
}
</script>

<style lang="scss">
@keyframes loading-spin {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(360deg);
  }
}
.sp-img-uploader {
  cursor: pointer;
  &__inner {
    position: relative;
    .ivu-icon {
      color: $color-brand-primary;
    }
    .img-file-item,
    .btn-upload {
      @include avatar(90px);
      float: none;
      padding: 0;
      margin: 0;
      border: 1px solid $color-border-gray-light;
    }
    .btn-upload {
      padding-top: 12px;
    }
    .ivu-icon-ios-loading {
      animation: loading-spin 1s linear infinite;
    }
  }
}
</style>
