<template>
  <div class="sp-coupon-picker">
    <Table
      :columns="columns"
      :data="list"
      @on-row-click="handleClickItem"
    >
      <template
        slot="selection"
        slot-scope="{ row, index }"
      >
        <Radio
          :key="index"
          :value="handleIsSelect(row, index)"
        />
      </template>
      <template
        slot="type"
        slot-scope="{ row }"
      >
        <span v-if="row.card_type == 'cash'"></span>
        <span v-if="row.card_type == 'discount'"></span>
      </template>
    </Table>
    <Page
      class="sp-picker-page"
      size="small"
      show-total
      :total="pageCount"
      :current="pageCur"
      :page-size="pageSize"
      @on-change="goPage"
    />
  </div>
</template>

<script>
import PageMixin from '../mixins/page'
import BasePicker from './base'
// import { resolveQuery, resolveImgTitle, updateItem, remove } from '../helper'

export default {
  extends: BasePicker,

  //   components: { Img, TagModal, ImgUploadModal, ImgEditList, ImgPage },

  mixins: [PageMixin],

  config: {
    title: '',
    type: 'coupon'
  },

  props: {
    multiple: Boolean
  },

  data () {
    const columns = [
      {
        slot: 'selection',
        width: '161'
      },
      {
        title: 'ID',
        key: 'card_id',
        width: '150'
      },
      {
        title: '',
        key: 'title',
        width: '300'
      },
      {
        title: '',
        slot: 'type',
        key: 'type'
      }
    ]
    return {
      pageSize: 10,
      columns,
      curId: '',
      list: [],
      selection: []
    }
  },

  computed: {},

  created () {
    this.nextPage()
  },

  methods: {
    async fetch ({ page_no, page_size }) {
      const { list, total_count } = await this.$api.picker.getCouponList({
        end_date: 1,
        page_no: page_no,
        page_size: page_size
      })
      this.list = list
      return { count: total_count }
    },
    handleClickItem (item) {
      if (this.multiple) {
        const findex = this.selection.findIndex((se) => se.card_id == item.card_id)
        if (findex > -1) {
          this.selection.splice(findex, 1)
        } else {
          this.selection.push(item)
        }
      } else {
        this.selection[0] = item
      }
      this.updateVal(this.selection)
    },
    handleIsSelect (item, index) {
      if (this.multiple) {
        const findex = this.selection.findIndex((se) => se.card_id == item.card_id)
        return findex > -1
      } else {
        return this.selection.length == 1 ? this.selection[0] == item.card_id : false
      }
    }
  }
}
</script>

<style lang="scss">
.sp-coupon-picker {
}
</style>
