<style lang="scss"></style>
<template>
  <div class="sp-path-page-picker">
    <Table
      :columns="columns"
      :data="list"
      @on-row-click="handleClickItem"
    >
      <template
        slot="selection"
        slot-scope="{ row, index }"
      >
        <Radio
          :key="index"
          :value="curId === row.id"
        />
      </template>
    </Table>
    <!-- <Page
      class="sp-picker-page"
      size="small"
      show-total
      :total="pageCount"
      :current="pageCur"
      :page-size="pageSize"
      @on-change="goPage"
    /> -->
  </div>
</template>

<script>
// import PageMixin from '../mixins/page'
export default {
  // mixins: [PageMixin],
  name: '',
  props: {
    onSelect: Function,
    data: [Object, Array, String, Number, Boolean]
  },
  data () {
    const columns = [
      {
        slot: 'selection',
        width: '161'
      },
      {
        title: 'ID',
        key: 'id',
        width: '250'
      },
      {
        title: '',
        key: 'title',
        width: '250'
      }
    ]
    return {
      columns,
      curId: '',
      list: [
        {
          title: '',
          id: '1',
          url: '/'
        },
        {
          title: '',
          id: '2',
          url: '/items'
        },
        {
          title: '',
          id: '3',
          url: '/member/user-info'
        },
        {
          title: '',
          id: '4',
          url: '/cart'
        },
        {
          title: '',
          id: '5',
          url: '/pointitems'
        }
      ]
    }
  },
  created () {},
  mounted () {
    this.curId = this.data.id
    console.log('path-page-picker init data: ', this.data)
  },
  methods: {
    handleClickItem (item) {
      this.curId = item.id
      this.onSelect(item)
    }
  }
}
</script>
