<style lang="scss" src="./index.scss"></style>

<template>
  <div class="sp-toolbar">
    <div class="sp-toolbar__left">
      <template v-for="(item, index) in leftTools">
        <Tooltip
          :key="index"
          class="tool-items"
          :content="item.tooltip"
          :disabled="!item.tooltip"
        >
          <span
            v-if="!item.component"
            class="menu-item"
            @click="handleClick(item.action)"
          >
            <div class="menu-item__icon">
              <SpIcon :icon="item.icon" />
            </div>
            <span class="menu-item__name">{{ item.text }}</span>
          </span>
          <template v-else>
            <component
              :is="item.component"
              :key="index"
              :icon="item.icon"
              :text="item.text"
            />
          </template>
        </Tooltip>
      </template>
    </div>
    <div class="sp-toolbar__right">
      <template v-for="(item, index) in rightTools">
        <Tooltip
          :key="index"
          class="tool-items"
          :content="item.tooltip"
          :disabled="!item.tooltip"
        >
          <span
            v-if="!item.component"
            class="menu-item"
            @click="handleClick(item.action)"
          >
            <div class="menu-item__icon">
              <SpIcon :icon="item.icon" />
            </div>
            <span class="menu-item__name">{{ item.text }}</span>
          </span>
          <template v-else>
            <component
              :is="item.component"
              :key="index"
              :icon="item.icon"
              :text="item.text"
            />
          </template>
        </Tooltip>
      </template>
    </div>
    <!-- <DemoPicker style="display:inline-block; vertical-align: top; max-width: 900px;" /> -->
  </div>
</template>

