export default {
  name: 'SpToolBar',
  // components: { DemoPicker },
  props: {},
  data () {
    return {
      leftTools: [],
      rightTools: []
    }
  },
  mounted () {
    this.leftTools = S.leftTools
    this.rightTools = S.rightTools
    console.log(this.leftTools, this.rightTools)
  },
  computed: mapState({
    zoom: (state) => state.zoom
  }),
  methods: {
    handleClick (action) {
      action && action()
    }
  }
}
