<template>
  <div class="sp-items-picker">
    <div class="sp-items-picker-toolbar">
      <Form
        class="sp-items-picker-search"
        @submit.native.prevent="handleSubmit"
      >
        <Select
          v-model="query.cat_id"
          placeholder=""
        >
          <Option
            v-for="item in categoryOptions"
            :key="item.id"
            :value="item.id"
          >
            {{ item.prefix }}{{ item.name }}
          </Option>
        </Select>
        <Input
          v-model="query.name"
          placeholder=""
        />
        <Input
          v-model="query.bn"
          placeholder=""
        />
        <Button
          type="primary"
          html-type="submit"
        >
          
        </Button>
        <Button @click="handleSelectAll">
          
        </Button>
        <Button
          type="text"
          @click="handleResetQuery"
        >
          
        </Button>
      </Form>
    </div>

    <div class="sp-items-picker-cont">
      <template v-if="list.length > 0">
        <div
          v-for="item in list"
          :key="item.item_id"
          class="goods-item"
          :class="{ 'is-active': selectionDict[item.item_id] }"
          @click="handleClickItem(item)"
        >
          <div class="goods-item__inner">
            <div class="goods-item__img">
              <Img :src="item.img" />
            </div>
            <div class="goods-item__title">
              {{ item.title }}
            </div>
          </div>
        </div>
      </template>
      <div
        v-else
        class="note"
      >
        
      </div>
    </div>
    <Page
      class="sp-picker-page"
      size="small"
      show-total
      :total="pageCount"
      :current="pageCur"
      :page-size="pageSize"
      @on-change="goPage"
    />
  </div>
</template>

<script>
import { findIndex } from 'lodash'
import PageMixin from '../mixins/page'
import BasePicker from './base'
import Img from '../comps/img'
import { resolveQuery } from '../helper'

const resolveCategoryOptions = (data) => {
  const walk = (node, arr) => {
    const { level, cat_id: id, cat_name: name } = node
    arr.push({
      prefix: level > 1 ? ''.repeat(+level - 1) : '',
      level,
      id,
      name
    })

    if (node.child_count > 0) {
      node[`lv${1 + +level}`].forEach((n) => walk(n, arr))
    }
  }

  const items = []
  data.forEach((n) => walk(n, items))
  return items
}

const defaultQuery = () => ({
  name: '',
  bn: '',
  cat_id: ''
})

export default {

  components: { Img },
  extends: BasePicker,

  mixins: [PageMixin],

  config: {
    type: 'goods'
  },

  data () {
    return {
      defaultVal: [],
      list: [],
      categoryOptions: [],
      query: defaultQuery()
    }
  },

  computed: {
    selection () {
      return !this.localVal ? [] : this.localVal.data
    },
    selectionDict () {
      const key = 'item_id'
      const selection = this.selection || []
      return selection.reduce((val, item) => ({ ...val, [item[key]]: true }), {})
    }
  },

  created () {
    this.fetchOptions()
    this.refresh()
  },

  methods: {
    async fetchOptions () {
      const data = await this.$api.picker.itemCategory()
      const categoryOptions = resolveCategoryOptions(data)
      this.categoryOptions = categoryOptions
    },
    async fetch (params) {
      if (this.loading) return
      this.loading = true

      params = {
        ...params,
        q: resolveQuery(this.query)
      }

      try {
        const { list, count } = await this.$api.picker.items(params)
        const resolveItem = (t) => {
          const { item_id, title, image_default_id: img } = t
          return {
            item_id,
            title,
            img
          }
        }

        this.list = list.map(resolveItem)
        this.loading = false
        return {
          count
        }
      } catch (e) {
        this.loading = false
        return {
          count: this.pageCount || 0
        }
      }
    },
    async handleSubmit () {
      if (this.loading) return
      this.refresh(true)
    },
    handleSelectAll () {
      if (this.loading) return
      let selection = []
      if (this.selection.length === 0 || this.selection.length !== this.localVal.data.length) {
        selection = [...this.list]
      }
      this.updateVal(selection)
    },
    handleResetQuery () {
      this.query = defaultQuery()
    },
    handleClickItem (item) {
      if (this.loading) return
      const idx = findIndex(this.selection, (s) => s.item_id === item.item_id)
      if (idx >= 0) {
        this.selection.splice(idx, 1)
      } else {
        this.selection.push(item)
      }
      this.updateVal(this.selection)
    }
  }
}
</script>

<style lang="scss">
.sp-items-picker {
  &-toolbar {
    padding: 10px 0;
  }
  &-search {
    display: flex;
    align-items: center;
    .ivu-select,
    .ivu-input-wrapper,
    .ivu-btn {
      margin-right: 10px;
    }
    .ivu-select,
    .ivu-input-wrapper {
      flex: 2;
    }
    .ivu-btn {
      flex: 1;
    }
  }
  &-cont {
    background: #f8f8f8;
    overflow: hidden;
    padding: 5px;
    min-height: 340px;
  }

  .goods-item {
    background: #fff;
    float: left;
    width: 120px;
    height: 160px;
    margin: 5px;
    text-align: center;
    cursor: pointer;
    transition: all 0.3s;
    &__img {
      width: 110px;
      height: 110px;
      overflow: hidden;
      margin: 0 0 5px;
    }
    &__title {
      @include mult-ellipsis(2);
      line-height: 1.4;
    }
    &__inner {
      padding: 5px;
    }
    &:hover {
      background: lighten($color-brand-primary, 40%);
    }
    &.is-active {
      background: lighten($color-brand-primary, 36%);
    }
    .picker-img {
      max-width: 100%;
      max-height: 100%;
    }
  }
}
</style>
