<template>
  <div class="sp-article-picker">
    <Form :label-width="80">
      <FormItem label="">
        <Select
          placeholder=""
          :value="curId"
          filterable
          @input="handleChange"
        >
          <Option
            v-for="item in list"
            :key="item.article_id"
            :value="item.article_id"
          >
            {{
              item.title
            }}
          </Option>
        </Select>
      </FormItem>
    </Form>
  </div>
</template>

<script>
import BasePicker from './base'
import { resolveSelectVal } from '../helper'

const ID_KEY = 'article_id'

export default {
  extends: BasePicker,

  config: {
    title: '',
    type: 'article'
  },

  data () {
    return {
      list: [],
      curId: resolveSelectVal(this.value, ID_KEY)
    }
  },

  created () {
    this.fetch()
  },

  methods: {
    async fetch () {
      const list = await this.$api.picker.article()
      this.list = list
      if (list.length > 0) {
        const data = this.list[0]
        this.curId = data[ID_KEY]
        this.updateVal(data)
      }
    },
    handleChange (val) {
      const data = this.list.find((t) => t[ID_KEY] === val)
      this.updateVal(data)
    }
  }
}
</script>

<style></style>
