<style lang="scss">
.sp-pagemode {
  .menu-item__icon {
    i {
      &.active {
        color: #2c98f0;
      }
    }
  }
}
</style>
<template>
  <div class="sp-pagemode">
    <span
      class="menu-item"
      @click="handleClick"
    >
      <div class="menu-item__icon">
        <SpIcon
          :icon="icon"
          :class="{ active: pageMode === 'h5' }"
        />
      </div>
      <span class="menu-item__name">{{ text }}</span>
    </span>
  </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
export default {
  name: 'SpPagemode',
  props: {
    icon: String,
    text: String
  },
  computed: mapState({
    pageMode: (state) => state.pageMode
  }),
  methods: {
    ...mapMutations(['PAGE_MODE']),
    handleClick () {
      this.PAGE_MODE()
    }
  }
}
</script>
