<style lang="scss">
  .widget-W0020-props {
    .wgt-props {
      display: flex;
      align-items: center;
      margin: 20px 0 10px;

      &_img {
        flex: 0;
        width: 60px;
        height: 60px;
        cursor: pointer;

        img {
          width: 60px;
          height: 60px;
        }
      }

      &_delete {
        flex: 0;
        width: 30px;
        cursor: pointer;
        padding-top: 5px;
        text-align: center;

        .icon-shanchu {
          color: #3091f2;
        }
      }

      &_content {
        flex: 1;
        padding: 5px 10px;

        .title {
          font-size: 12px;
          font-weight: 400;
          @include mult-ellipsis(2);
        }

        .tag {
          .tag-item {
            padding: 2px 3px;
            // border: 1px solid rgba(247, 137, 137, 1);
            border-radius: 3px;
            font-size: 12px;
            color: #f56c6c;
            background-color: rgba(254, 240, 240, 1);
            margin-right: 3px;
            // @include mult-ellipsis(1)
          }
        }
      }
    }

    .tip {
      margin-top: 8px;
      color: #d01717;
    }
  }
</style>
<template>
  <div class="widget-W0020-props">
    <Tabs value="name1" :animated="false">
      <TabPane :label="`${activeElement.name}[${activeElement.type}]`" name="name1" style="overflow: auto">
        <panelBar title="">
          <attr-field label="">
            <Input v-model="activeElement.text" />
          </attr-field>
          <div class="division"></div>
          <attrField label="">
            <RadioGroup v-model="activeElement.titleAlign">
              <Radio label="left">
                <span></span>
              </Radio>
              <Radio label="center">
                <span></span>
              </Radio>
              <Radio label="right">
                <span></span>
              </Radio>
            </RadioGroup>
          </attrField>
          <div class="division"></div>
          <attrField label="">
            <iSwitch size="small" v-model="activeElement.wgtHiddenTitle" />
          </attrField>
          <div class="division"></div>
          <attrField label="">
            <iSwitch size="small" v-model="activeElement.titleBackground" />
          </attrField>
          <div class="division"></div>
          <attr-field label="">
            <InputNumber v-model="activeElement.wgtTitleFontSize" size="small" :min="0"></InputNumber>
          </attr-field>
          <div class="division"></div>
          <attr-field label="">
            <color-picker v-model="activeElement.wgtTitleColor"></color-picker>
          </attr-field>
          <div class="division"></div>
          <attr-field label="">
            <color-picker v-model="activeElement.wgtBackgroundColor1"></color-picker>
            <color-picker v-model="activeElement.wgtBackgroundColor2"></color-picker>
          </attr-field>
        </panelBar>
        <panelBar title="">
          <div class="division"></div>
          <attrField :width = "100" label="">
            <iSwitch size="small" v-model="activeElement.wgtHideUnselectedItems" />
          </attrField>
          <div v-model="activeElement.data" v-for="(shop,i) in activeElement.data" :key="i" style="margin-top:10px;">
            <Collapse>
              <Panel :name="i.toString()">
                {{ i + 1 + '  :  ' + shop.shop_name }}
                <div slot="content">
                  <shopBind
                  :shopOnChange="shopOnChange.bind(this,i)"
                  style="width:100%;margin:0 auto;"></shopBind>
                  <div class="tip"> 538px * 96px</div>
                  <div class="division"></div>
                  <dataBind
                    :imgData="shop.shop_pic"
                    :pathData="shop.pathData"
                    :imgOnChange="handleImgOnChange.bind(this, i)"
                    :pathOnChange="handlePathOnChange.bind(this,i)">
                  </dataBind>
                  <div class="division"></div>
                  <div class="tip"></div>
                  <div class="tip"></div>
                  <ul>
                    <draggable v-model="activeElement.data[i].goods_list" class="list-group" ghost-class="ghost">
                      <li class="wgt-props" v-for="(item, index) in shop.goods_list" :key="`wgt-propsitem__${index}`">
                        <div class="wgt-props_img" @click="handleClickPickerGoods(shop, item, i, index)">
                          <img :src="item.goods_pic" />
                        </div>
                        <div class="wgt-props_content">
                          <h4 class="title">{{ item.goods_name }}</h4>
                        </div>
                      </li>
                    </draggable>
                  </ul>
                </div>
              </Panel>
            </Collapse>
          </div>
        </panelBar>
      </TabPane>
      <TabPane label="" name="name2">
        <basic-set v-model="activeElement"></basic-set>
      </TabPane>
    </Tabs>
  </div>
</template>
<script>
  import {
    panelBar,
    attrField,
    dataBind,
    colorPicker,
    shopBind
  } from '../../common/comps'
  import basicSet from '../../common/basic-set'
  export default {
    name: 'W0020Props',
    props: ['activeElement'],
    data() {
      return {}
    },
    components: {
      panelBar,
      attrField,
      dataBind,
      colorPicker,
      basicSet,
      shopBind
    },
    methods: {
      // 
      deleteSop(index) {
        this.$Modal.confirm({
          title: '',
          content: '',
          onOk: () => {
            if (this.activeElement.data.length > 0) {
              this.activeElement.data.splice(index, 1)
              this.$Message.success('')
            }
          }
        })
      },
      // 
      deleteItem(i, index) {
        this.$Modal.confirm({
          title: '',
          content: '',
          onOk: () => {
            if (this.activeElement.data.length > 0) {
              this.activeElement.data[i].goods_list.splice(index, 1)
              this.$Message.success('')
            }
          }
        })
      },
      // 
      handleImgOnChange(index, item) {
        this.activeElement.data[index].shop_pic = item
      },
      //
      handlePathOnChange(index, item) {
        this.activeElement.data[index].dataPath = item
      },
      async handleClickPickerGoods(shop, item, dix,index) {
        if(shop.shop_id == ''){
          this.$Message.error('')
          return
        }
        const {
          data
        } = await this.$picker('shop-goods', {
          data: JSON.parse(JSON.stringify(item)),
          shop:shop
        })
        const { goods_id,pics, price, item_name, linkPage, linkPageLabel ,market_price} = data
        const goodsData = {
          goods_id,
          goods_pic: pics.length > 0 ? pics[0] : '',
          price: price,
          goods_name: item_name,
          linkPage,
          linkPageLabel,
        }
        console.log(data)
        this.$set(this.activeElement.data[dix].goods_list, index,goodsData)
      },
      shopOnChange(index,data) {
        const {
          distributor_id,
          company_id,
          name,
          address,
          logo,
          banner,
          tagList,

        } = data;

        let shopData = {
          shop_id: distributor_id,
          shop_pic: '',
          pathData: {},
          shop_name: name,
          logo: logo,
          banner: banner,
          tag_list: tagList.length > 2?tagList.slice(0,2):tagList,
          goods_list: [{
              goods_id: '',
              goods_pic: '',
              price: 1799.00,
              goods_name: '1',
              pathData: {},
              linkPage: '',
              linkPageLabel: ''
            },
            {
              goods_id: '',
              goods_pic: '',
              price: 1799.00,
              goods_name: '2',
              pathData: {},
              linkPage: '',
              linkPageLabel: ''
            },
            {
              goods_id: '',
              goods_pic: '',
              price: 1799.00,
              goods_name: '3',
              pathData: {},
              linkPage: '',
              linkPageLabel: ''
            }
          ]
        };
        this.$set(this.activeElement.data, index, shopData)

      }

    }
  }
</script>
