import SpTab from './sp-tab'
import SpTabPanel from './sp-tabpanel'
import SpToolBar from './sp-toolbar'
import SpWidgetsPanel from './sp-widgetspanel'
import SpIcon from './sp-icon'
import SpViewPort from './sp-viewport'
import SpStylePanel from './sp-stylepanel'
import SpPrice from './sp-price'
import SpDptransition from './sp-dptransition'
import SpSetup from './sp-setup'
import SpPagemode from './sp-pagemode'
import SpZoom from './sp-zoom'
import SpAbout from './sp-about'
import SpPicker, { SpPickerPlugin } from './picker/index'

const components = {
  SpTab,
  SpTabPanel,
  SpToolBar,
  SpWidgetsPanel,
  SpIcon,
  SpViewPort,
  SpStylePanel,
  SpPrice,
  SpPicker,
  SpDptransition,
  SpSetup,
  SpPagemode,
  SpZoom,
  SpAbout
}

const plugins = {
  SpPickerPlugin
}

export default {
  install (Vue) {
    Object.keys(components).forEach((name) => {
      const comp = components[name]
      if (!comp.name) {
        throw new Error('component name required: ', comp)
      }
      Vue.component(comp.name, comp)
    })

    Object.keys(plugins).forEach((name) => {
      const plugin = plugins[name]
      Vue.use(plugin)
    })
  },
  ...components
}
