import Vue from 'vue'
import { savePageData, saveTemplateContent, saveHeaderOrFooter } from '@/api/temp'
import { log } from '@/utils'

export default {
  // 
  async SAVE_PAGEDATA ({ commit, dispatch, state }) {
    const { widgets, pageid } = state.pagedata
    let config = []
    widgets.forEach(wgt => {
      if (wgt.type) {
        config.push({
          name: wgt.type,
          config: wgt,
          data: wgt.data
        })
      }
    })

    const headerWidget = widgets.filter((item) => item.comptyle == 'header')
    const bodyWidget = widgets.filter((item) => {
      return !item.comptyle || item.comptyle == 'body'
    })
    const footerWidget = widgets.filter((item) => item.comptyle == 'footer')

    // const params = {
    //   template_name: 'pc',
    //   version: 'v1.0.1',
    //   config: JSON.stringify(config),
    //   page_name: 'index2'
    // }
    log.debug(`[savePageData]`, bodyWidget)
    Vue.prototype.$Loading.start()
    // 
    await saveHeaderOrFooter({
      page_name: 'header',
      config: JSON.stringify(headerWidget)
    })

    // 
    await saveHeaderOrFooter({
      page_name: 'footer',
      config: JSON.stringify(footerWidget)
    })

    // body
    await saveTemplateContent({
      theme_pc_template_id: pageid,
      config: JSON.stringify(bodyWidget)
    })
    Vue.prototype.$Loading.finish()

    Vue.prototype.$Message.success('')
  }
}
