<style lang="scss">
.sp-path-brand-picker {
  // .category-level__1 {
  //   padding-left: 0;
  // }
  .picker-hd {
    display: flex;
    margin-bottom: 10px;
  }
  .search-con {
    width: 50%;
  }
  .brand-logo {
    width: 40px;
    height: 40px;
    object-fit: cover;
    position: relative;
    top: 2px;
  }
}
</style>
<template>
  <div class="sp-path-brand-picker">
    <!-- <div class="picker-hd">
      <div class="search-con">
        <Input suffix="ios-search" v-model="keywords" @on-search="handleSearchOnChange" search size="large"  placeholder="" />
      </div>
    </div> -->
    <Table
      height="450"
      :columns="columns"
      :loading="loading"
      :data="list"
      @on-selection-change="handleClickItem"
      @on-row-click="handleClickItem"
    >
      <template
        slot="selection"
        slot-scope="{ row, index }"
      >
        <Radio :value="handleIsSelect(row, index)" />
      </template>

      <!-- <template slot-scope="{ row, index }" slot="image_url">
        <image :src="row.image_url" alt="" />
      </template> -->
    </Table>

    <Page
      class="sp-picker-page"
      size="small"
      show-total
      :total="pageCount"
      :current="pageCur"
      :page-size="pageSize"
      @on-change="goPage"
    />
  </div>
</template>

<script>
import PageMixin from '../mixins/page'
export default {
  name: '',
  mixins: [PageMixin],
  props: {
    onSelect: Function,
    data: [Object, Array, String, Number, Boolean]
  },
  data () {
    const columns = [
      {
        slot: 'selection',
        width: '80'
      },
      {
        title: 'ID',
        key: 'attribute_id',
        width: '100'
      },
      {
        title: 'logo',
        key: 'image_url',
        width: '150',
        render: (h, params) => {
          const { row } = params
          return h(
            'img',
            {
              class: 'brand-logo',
              attrs: {
                src: row.image_url
              }
            }
          )
        }
      },
      {
        title: '',
        key: 'attribute_name',
        width: '330'
      }
    ]

    return {
      columns,
      curId: '',
      list: [],
      datasource: [],
      selection: [],
      loading: true
    }
  },
  created () {},
  mounted () {
    if (this.$attrs.multiple) {
      this.selection = this.data
    } else {
      this.curId = this.data.attribute_id
    }
    this.nextPage()
  },
  methods: {
    async fetch ({ page_no, page_size }) {
      const { list, total_count } = await this.$api.picker.getItemListByBrand({
        pageSize: page_size,
        page: page_no,
        attribute_type: 'brand'
      })
      this.list = list
      this.loading = false
      return { count: total_count }
    },
    handleSearchOnChange () {
      this.refresh(true)
    },
    handleClickItem (item) {
      // console.log('pickerItem', item)
      if (this.$attrs.multiple) {
        const findex = this.selection.findIndex(se => se.attribute_id == item.attribute_id)
        if (findex > -1) {
          this.selection.splice(findex, 1)
        } else {
          this.selection.push(item)
        }
        // console.log('this.selection', this.selection)
      } else {
        this.curId = item.attribute_id
        this.selection = item
      }
      this.onSelect && this.onSelect(this.selection)
    },

    //
    handleIsSelect (item, index) {
      if (this.$attrs.multiple) {
        const findex = this.selection.findIndex(se => se.attribute_id == item.attribute_id)
        return findex > -1
      } else {
        return this.curId == item.attribute_id
      }
    }
  }
}
</script>
