import mshopConfig from '@/mshop.config'
import undoRedoHistory from '@/store/plugins/undo-redo/history'
class Spx {
  constructor () {
    this.$vue = null
    this.$router = null
    this.$store = null
    this.leftTools = []
    this.rightTools = []
    this.init()
  }

  init () {
    const { leftTools, rightTools } = mshopConfig
    this.leftTools = leftTools
    this.rightTools = rightTools
  }

  trigger (key, obj) {
    this[key] = obj
  }

  // 
  regTools (tools) {
    this.tools = [...this.tools, ...tools]
  }

  handleCommit (name, payload) {
    this.$store.commit(name, payload)
  }

  handleAction (name, payload) {
    this.$store.dispatch(name, payload)
  }

  // // 
  // preview () {
  //   window.open(`/preview`)
  // }

  // 
  undo () {
    undoRedoHistory.undo()
  }

  // 
  redo () {
    undoRedoHistory.redo()
  }
}

export default new Spx()
