<style lang="scss">
.sp-path-tag-picker {
  .picker-hd {
    display: flex;
    margin-bottom: 10px;
  }
  // .shop-picker-con {
  //   flex: 1;
  //   padding-right: 10px;
  // }
  .search-con {
    width: 50%;
  }
  .select-count{
    height: 36px;
    line-height: 36px;
    margin-left: 40px;
  }
}
</style>
<template>
  <div class="sp-path-tag-picker">
    <div class="picker-hd">
      <div class="search-con">
        <Input
          v-model="keywords"
          suffix="ios-search"
          search
          size="large"
          placeholder=""
          @on-search="handleSearchOnChange"
        />
      </div>
      <div class="select-count"> {{selection.length}}</div>
    </div>
    <Table
      :columns="columns"
      height="450"
      :data="list"
      @on-selection-change="handleClickItem"
      @on-row-click="handleClickItem"
    >
      <template
        slot="selection"
        slot-scope="{ row, index }"
      >
        <Radio :value="handleIsSelect(row, index)" />
      </template>
    </Table>
    <Page
      class="sp-picker-page"
      size="small"
      show-total
      :total="pageCount"
      :current="pageCur"
      :page-size="pageSize"
      @on-change="goPage"
    />
  </div>
</template>

<script>
import PageMixin from '../mixins/page'
import BasePicker from './base'
export default {
  name: '',
  mixins: [PageMixin],
  extends: BasePicker,

  config: {
    title: '',
    type: 'shop-tag'
  },
  props: {
    onSelect: Function,
    data: [Object, Array, String, Number, Boolean]
  },
  data () {

    const columns = [
      {
        slot: 'selection',
        width: '120'
      },
      {
        title: 'ID',
        key: 'tag_id',
        width: '140'
      },
      {
        title: '',
        key: 'tag_name',
        width: '534'
      }
    ]

    return {
      columns,
      curId: '',
      pageSize: 10,
      list: [],
      keywords: '',
      selection: []
    }
  },
  created () {},
  mounted () {
    this.nextPage()
    if (this.$attrs.multiple) {
      this.selection = this.$attrs.selectList
    } else {
      this.curId = this.$attrs.tag_id
    }
    console.log('path-tag-picker init data: ', this.selection)
  },
  methods: {
    async fetch ({ page_no, page_size }) {
      const { allList } = this.$attrs
      let list = allList.filter(item=>{
        return this.keywords == '' ? true : item.tag_name.includes(this.keywords)
        })
      this.list = this.getPagedTagList(page_no, page_size, list)
      return { count: allList.length }
    },
    getPagedTagList(page_no, page_size, tagList , ){
      let start = (page_no -1 ) * page_size
      let end = tagList.length % page_size
      end = start < tagList.length / page_size ? page_size : end == 0 ? page_size : end
      let list = tagList.slice(start,start + end)
      return list
    },
    handleSearchOnChange () {
      this.refresh()
    },
    handleClickItem (item) {
      // console.log('pickerItem', item)
      if (this.$attrs.multiple) {
        const findex = this.selection.findIndex(se => se.tag_id == item.tag_id)
        if (findex > -1) {
          this.selection.splice(findex, 1)
        } else {
          this.selection.push(item)
        }
        // console.log('this.selection', this.selection)
      } else {
        this.curId = item.tag_id
        this.selection = item
      }
      this.updateVal(this.selection)
    },
    //
    handleIsSelect (item, index) {
      if (this.$attrs.multiple) {
        const findex = this.selection.findIndex(se => se.tag_id == item.tag_id)
        return findex > -1
      } else {
        return this.curId == item.tag_id
      }
    },
    handleConfirm() {
      console.log("aaaa");
      debugger
      this.$emit('on-change', this.selection)
    },
  }
}
</script>
