<style lang="scss"></style>
<template>
  <i :class="[iconname, icon]" />
</template>

<script>
import mshopConfig from '@/mshop.config'
export default {
  name: 'SpIcon',
  props: {
    icon: String
  },
  data () {
    return {
      iconname: ''
    }
  },
  created () {
    const { iconfont } = mshopConfig
    this.iconname = iconfont
  }
}
</script>
