import Vue from 'vue'
import Vuex from 'vuex'
import mutations from './mutation'
import actions from './actions'
import undoRedoPlugin from './plugins/undo-redo'
// import VuexPersistence from 'vuex-persist'
import { find, isEmpty } from 'lodash'
Vue.use(Vuex)

const state = {
  // 
  pageMode: 'pc',
  // zoom
  zoom: 100,
  // 
  pageWidth: 1190,
  // 
  pageHeight: 'auto',
  // 
  minHeight: 800,
  // 
  showRuler: true,
  // 
  showLine: true,
  // activeid
  activeEleId: null,
  // 
  preview: false,
  // activeid
  // activeEleIdOfPanel: null,
  // 
  // activeElement: {},
  // // 
  // activePage: {},
  // updateRoute: null,
  // // 
  // privilegeAddCombine: false,
  // 
  pagedata: {
    name: '',
    title: '',
    widgets: [],
    nav: {}
  }
}

const getters = {
  // 
  getActiveWidget: (state) => {
    if (state.pagedata && state.activeEleId) {
      const { widgets } = state.pagedata
      let result
      for (let i = 0; i < widgets.length; i++) {
        if (widgets[i].uuid === state.activeEleId) {
          result = widgets[i]
          break
        } else {
          if (widgets[i].childWidgets) {
            for (let j = 0; j < widgets[i].childWidgets.length; j++) {
              if (widgets[i].childWidgets[j].uuid === state.activeEleId) {
                result = widgets[i].childWidgets[j]
                break
              }
            }
          }
        }
      }
      return result
    } else {
      return null
    }
  }
}

// const vuexLocal = new VuexPersistence({
//   key: '__presist_mshop',
//   storage: window.localStorage
//   // reducer: (state) => ({
//   //   state: state
//   // })
// })

export default new Vuex.Store({
  state,
  getters,
  mutations,
  actions,
  plugins: [undoRedoPlugin]
})
