<template>
  <Modal
    v-model="show"
    class="sp-img-upload-modal"
    title=""
    :loading="loading"
    :mask-closable="false"
    @on-ok="handleConfirm"
    @on-cancel="handleCancel"
  >
    <Form
      ref="form"
      :label-width="100"
      :model="model"
      :rules="rules"
      @submit.native.prevent="handleConfirm"
    >
      <FormItem
        label=""
        prop="group"
      >
        <Select v-model="model.group">
          <Option
            v-for="group in groups"
            :key="group.group_id"
            :value="group.group_id"
          >
            {{
              group.title
            }}
          </Option>
        </Select>
      </FormItem>
      <FormItem
        label=":"
        prop="img"
      >
        <ImgUpload
          ref="imgUpload"
          v-model="model.img"
        />
        <div class="hint">
           <br>
          1jpg/png/gif <br>
          22MB
        </div>
      </FormItem>
      <FormItem
        label=""
        prop="tags"
      >
        <TagInput
          v-model="model.tags"
          :list="tags"
        />
      </FormItem>
    </Form>
  </Modal>
</template>

<script>
import TagInput from './tag-input'
import ImgUpload from './img-upload'

const defaultModel = (params = {}) => {
  return {
    img: '',
    ...params
  }
}

export default {
  components: { TagInput, ImgUpload },

  props: {
    noGroup: Boolean,
    noTag: Boolean
  },

  data () {
    return {
      show: false,
      loading: true,
      model: null,
      groups: [],
      tags: [],
      rules: {
        group: [{ required: true, message: '' }],
        img: [{ required: true, message: '' }],
        tags: [{ type: 'array', message: '' }]
      }
    }
  },

  created () {
    this.resetModel()
    this.fetch()
  },

  methods: {
    resetModel () {
      const params = {}
      if (!this.noGroup) params.group = ''
      if (!this.noTag) params.tags = []
      this.model = defaultModel(params)
    },
    async fetch () {
      const [groups, tags] = await Promise.all([
        this.$api.picker.imgGroup(),
        this.$api.picker.imgTag()
      ])
      this.groups = groups
      this.tags = tags
    },
    handleClose () {
      this.show = false
      this.$refs.form.resetFields()
      this.$refs.imgUpload.reset()
    },
    handleConfirm () {
      this.$refs.form.validate((valid) => {
        if (valid) {
          this.handleClose()
          this.$emit('change', { ...this.model })
        } else {
          this.loading = false
          this.$nextTick(() => {
            this.loading = true
          })
        }
      })
    },
    handleCancel () {
      this.handleClose()
    }
  }
}
</script>

<style lang="scss">
.sp-img-upload-modal {
  .hint {
    color: $color-gray;
    line-height: 1.5;
  }
}
</style>
