<template>
  <Select
    ref="select"
    multiple
    filterable
    remote
    :remote-method="handleFilter"
    :loading="loading"
    :value="value"
    @keydown.native.enter.prevent="handleConfirm"
    @on-change="handleChange"
    @on-open-change="handleOpenChange"
  >
    <Option
      v-for="option in filteredOptions"
      :key="option.value"
      :value="option.value"
    >
      {{
        option.label
      }}
    </Option>
  </Select>
</template>

<script>
import { difference, cloneDeep } from 'lodash'
import { remove } from '../helper'
import { Select as iSelect } from 'iview'

let cid = 1

const Select = cloneDeep(iSelect)
Select.watch.selectOptions = function () {
  if (this.hasExpectedValue && this.selectOptions.length > 0) {
    if (this.values.length === 0) {
      this.values = this.getInitialValue()
    }
    const getOptionData = (val) => {
      const v = val.value ? val.value : val
      return this.getOptionData(v)
    }
    this.values = this.values.map(getOptionData).filter(Boolean)
    this.hasExpectedValue = false
  }
  if (this.slotOptions && this.slotOptions.length === 0) {
    this.query = ''
  }

  //  dropdown ()
  // dropdown 
  this.broadcast('Drop', 'on-update-popper')
}

export default {
  components: { Select },

  props: {
    value: Array,
    list: Array,
    default: () => []
  },

  data () {
    return {
      loading: false,
      filteredOptions: [],
      userOptions: []
    }
  },

  computed: {
    options () {
      const options = this.list.map(({ tag_id: value, title: label }) => ({
        label,
        value
      }))
      const ret = [...this.userOptions, ...options]
      return ret
    }
  },

  created () {
    this.lastValue = []
  },

  beforeDestroy () {
    this.lastValue = null
  },

  methods: {
    makeOption (label) {
      cid++
      return {
        label,
        value: `cid_${cid}`
      }
    },
    handleFilter (query = '') {
      this.filteredOptions =
        query !== ''
          ? this.options.filter((t) => t.label.toLowerCase().indexOf(query.toLowerCase()) > -1)
          : this.options.slice()
    },
    handleOpenChange (val) {
      if (val && this.$refs.select.query === '') {
        this.filteredOptions = this.options.slice()
      }
    },
    handleChange (val) {
      if (this.lastValue) {
        const diff = difference(this.lastValue, val)
        if (diff.length) {
          let hasUserOption
          diff.forEach((val) => {
            if (remove(this.userOptions, (item) => item.value === val)) {
              hasUserOption = true
            }
          })
          if (hasUserOption) this.handleFilter(this.$refs.select.query)
        }
      }
      this.lastValue = val
      this.$emit('input', val)
    },
    handleConfirm (e) {
      const query = this.$refs.select.query.trim()
      if (query === '') return

      const idx = this.options.findIndex((t) => t.label === query)
      if (idx >= 0) return

      const option = this.makeOption(query)
      this.userOptions.push(option)
      this.$nextTick(() => {
        this.$refs.select.values = this.$refs.select.values.concat(option)
      })

      const val = this.value.concat(option.value)
      this.$emit('input', val)
    }
  }
}
</script>

