<template>
  <Modal
    v-model="isLocalShow"
    class="sp-picker"
    :title="title"
    :width="width"
    :transfer="false"
    :mask-closable="false"
    @on-ok="handleConfirm"
  >
    <PickerType
      v-if="isLocalShow"
      ref="picker"
      :value="value"
      v-bind="extra"
      @close="handleToggle(false)"
    />
  </Modal>
</template>

<script>
import PickerType from './picker-type'

export default {
  name: 'SpPicker',

  components: { PickerType },

  props: {
    value: Object,
    width: {
      default: 830
    },
    isShow: Boolean,
    extra: Object
  },

  data () {
    const { type } = this.value
    const { title } = PickerType.resolvePickerConfig(type)

    return {
      title,
      isLocalShow: false
    }
  },

  watch: {
    isShow: {
      immediate: true,
      handler (val) {
        this.isLocalShow = val
      }
    }
  },

  methods: {
    handleToggle (isShow) {
      if (isShow === undefined) isShow = !this.isLocalShow
      this.isLocalShow = isShow
      this.$emit('toggle', isShow)
      if (!isShow) {
        this.$emit('close')
      }
    },
    handleConfirm () {
      const val = this.$refs.picker.getVal()
      this.$emit('input', val)
    }
  }
}
</script>
