<script>
import { GoodsPicker, CategoryPicker, ArticlePicker, PagePicker, ImgPicker, PathPicker, CouponPicker } from './pickers'
import { ShopPicker, ShopGoodsPicker, ShopTagsPicker} from './pickers'
function resolvePicker (type) {
  switch (type) {
    case 'goods':
    case 'goods-tag':
      return GoodsPicker
    case 'category':
      return CategoryPicker
    case 'article':
      return ArticlePicker
    case 'page':
      return PagePicker
    case 'img':
      return ImgPicker
    case 'path':
      return PathPicker
    case 'coupon':
      return CouponPicker
    case 'shop':
      return ShopPicker
    case 'shop-goods':
      return ShopGoodsPicker
    case 'shop-tag':
      return ShopTagsPicker
    default:
      throw new Error(`picker: ${type} not registered`)
      // break
  }
}

export default {
  functional: true,

  render (h, ctx) {
    const { type } = ctx.props.value
    const data = {
      ...ctx.data,
      value: ctx.data.attrs.value
    }
    return h(resolvePicker(type), data, ctx.children)
  },

  resolvePickerConfig (type) {
    const Picker = resolvePicker(type)
    return Picker.config || {}
  }
}
</script>
