<style lang="scss">
.sp-price {
  display: inline-block;
  vertical-align: middle;
  &__small {
    font-size: $font-size-small;
  }
  &__default {
    font-size: $font-size;
  }
  &__large {
    font-size: $font-size-large;
  }
  font-family: Arial;
  .price__symbol {
    margin-right: 4px;
    float: left;
  }
  .price__int,
  .price__decimal,
  .price__append {
    float: left;
    text-decoration: inherit;
  }
}
</style>

<template>
  <span :class="classes">
    <!-- {{ value < 0 ? '-' : '' }} -->
    <template v-if="isValid">
      <span
        v-if="!noSymbol"
        class="price__symbol"
      >{{ symbol }}</span>
      <span class="price__int">{{ priceVals.int }}</span>
      <span
        v-if="!noDecimal && priceVals.decimal !== undefined"
        class="price__decimal"
      >.{{ priceVals.decimal }}</span>
      <span
        v-if="appendText"
        class="price__append"
      >{{ appendText }}</span>
    </template>
    <template v-else>{{ defaultValue }}</template>
  </span>
</template>

<script>
import { oneOf, isNumber } from '@/utils'

const prefixCls = 'sp-price'

export default {
  name: 'SpPrice',
  props: {
    symbol: {
      type: String,
      default: ''
    },
    value: {
      type: [String, Number],
      default: ''
    },
    defaultValue: {
      type: String,
      default: '-'
    },
    size: {
      validator (value) {
        return oneOf(value, ['small', 'large', 'default'])
      },
      default () {
        return 'default'
      }
    },
    noSymbol: Boolean,
    noDecimal: Boolean,
    unit: String,
    appendText: String
  },
  computed: {
    isValid () {
      return !isNaN(parseFloat(this.value, 10))
    },
    classes () {
      return `${prefixCls} ${prefixCls}__${this.size} clearfix`
    },
    priceVals () {
      const value = +this.value
      let priceVal = this.unit === 'cent' ? value / 100 : value
      if (isNumber(priceVal)) {
        priceVal = priceVal.toFixed(2)
      }
      const [int, decimal] = (priceVal || '').split('.')
      return {
        int,
        decimal
      }
    }
  }
}
</script>
