<style lang="scss">
  .shop-picker {
    .modal-bd {

      // padding: 10px 0;
      .store-wrap {
        height: 400px;
        overflow-y: auto;
      }

      .store-item {
        // margin-bottom: 10px;
        cursor: pointer;
        padding: 10px;
        display: flex;
        align-items: center;

        &:hover {
          background: #f0faff;
        }

        .store-name {
          font-size: 14px;
        }
      }
    }
  }
</style>
<template>
  <div class="shop-picker">
    <div class="modal-bd">
      <Table
        :columns="columns"
        height="450"
        :data="list"
        @on-selection-change="handleClickItem"
        @on-row-click="handleClickItem"
      >
        <template
          slot="selection"
          slot-scope="{ row, index }"
        >
        <Radio :value="selection ? row.goods_id == selection.goods_id : false" />

        </template>
      </Table>
      <Page class="sp-picker-page" size="small" show-total :total="pageCount" :current="pageCur" :page-size="pageSize"
        @on-change="goPage" />
    </div>

  </div>
</template>

<script>
  import regions from '../comps/district.json'
  import PageMixin from '../mixins/page'
  import BasePicker from './base'
  import {
    resolveSelectVal
  } from '../helper'
  export default {
    name: '',
    mixins: [PageMixin],
    extends: BasePicker,
    config: {
      title: '',
      type: 'shop'
    },
    props: {
      // onCustomChange: Function,
      test: String
    },
    data() {
      const { shop } =this.$attrs

      const columns = [
        {
          slot: 'selection',
          width: '120'
        },
        {
          title: 'ID',
          key: 'goods_id',
          width: '140'
        },
        {
          title: '',
          key: 'itemName',
          width: '534'
        }
      ]
      return {
        columns,
        loading: true,
        show: false,
        regions: [],
        list: [],
        page_size: 10,
        selection: null,
        shop:shop,
        keywords: ''
      }
    },
    created() {},
    mounted() {
      this.regions = regions
      this.nextPage()
    },
    methods: {
      async fetch({
        page_no,
        page_size
      }) {
        const {
          list,
          total_count
        } = await this.$api.picker.goodsItems({
          pageSize: page_size,
          page: page_no,
          item_type: 'normal',
          keywords: this.keywords,
          distributor_id: this.shop ? this.shop.shop_id : ''
        })
        this.list = list
        return {
          count: total_count
        }
      },
      handleSearchOnChange() {
        this.refresh()
      },
      handleDeleteShop() {
        this.selection = null
        this.handleConfirm()
      },
      handleConfirm() {
        // this.onCustomChange && this.onCustomChange(this.selection)
        this.$emit('on-change', this.selection)
      },
      handleCancel() {},
      handleClickItem(item) {
        this.selection = item
        this.updateVal(item)
      }

    }
  }
</script>
