  // 
  SET_PAGEDATA (state, payload) {
    state.pagedata = payload
    // W0001
    // const { widgets } = payload
    // const wpage = widgets.find(item => item == 'W0000')
    // if (wpage) {
    //   state.pageBackground = wpage.backgroundColor
    // }
  },
  // 
  RESET_PAGEDATA (state, payload) {
    state.pagedata = {
      name: '',
      title: '',
      widgets: [],
      nav: {}
    }
  },
  // 
  PREVIEW (state, payload) {
    state.preview = payload.preview
  },
  // 
  EXIT (state, payload) {
    window.vm.$router.go(-1)
  },
