  // 
  ADD_WIDGET (state, payload) {
    // 
    payload.setting.width = state.pageWidth
    const setting = JSON.parse(JSON.stringify(payload.setting))
    state.pagedata.widgets.push({
      ...setting,
      uuid: CreateID()
    })
    // W0000
    if (payload.setting.type == 'W0000') {
      state.pageBackground = payload.setting.backgroundColor
    }
  },
  // 
  DELETE_WIDGET (state, payload) {
    state.pagedata.widgets.forEach((item, index) => {
      if (item.uuid === payload) {
        state.pagedata.widgets.splice(index, 1)
        // state.activeElement = {}
      }
    })
  },
  // 
  SELECT_ELE (state, payload) {
    state.activeEleId = payload
  },
  // 
  CHANGE_WIDGET_SORT (state, payload) {
    const { index1, index2 } = payload
    const { widgets } = state.pagedata
    let temp = widgets[index1]
    // state.pagedata.widgets[index1] = widgets[index2]
    // state.pagedata.widgets[index2] = temp
    Vue.set(widgets, index1, widgets[index2])
    Vue.set(widgets, index2, temp)
  },
  // 
  ZOOM_UP (state) {
    state.zoom = parseInt(state.zoom / 10) * 10 + 10
  },
  // 
  ZOOM_DOWN (state) {
    state.zoom = parseInt(state.zoom / 10) * 10 - 10
  },
  // 
  ZOOM_SET (state, playload) {
    state.zoom = playload
  },
  // 
  ZOOM_MIDDLE (state) {
    const { clientWidth } = document.getElementById('viewport-container')
    state.zoom = parseInt((clientWidth / (state.pageWidth + 200)) * 100)
  },
  // 
  PAGE_SETUP (state) {
    const { widgets } = state.pagedata
    const wpage = widgets.find(item => item.type == 'W0000')
    state.activeEleId = wpage.uuid
  },
  // 
  SET_RULER (state) {
    state.showRuler = !state.showRuler
  },
  // 
  SET_LINE (state) {
    state.showLine = !state.showLine
  },
  // 
  PAGE_MODE (state) {
    const pageMode = state.pageMode === 'h5' ? 'pc' : 'h5'
    state.pageMode = pageMode
    if (pageMode == 'pc') {
      state.pageWidth = 1190
      state.pageHeight = 'auto'
      state.minHeight = 800
    } else {
      state.pageWidth = 375
      state.pageHeight = 667
      state.minHeight = 'auto'
    }
    // widget page 
    const { widgets } = state.pagedata
    const wgtPage = widgets.find((widget) => widget.type == 'WgtPage')
    wgtPage['width'] = state.pageWidth
  }

