import req from './req'

export function getPageData (params) {
  return req.get('/wxa/pageparams/setting?template_name=pc&version=v1.0.1&page_name=index', params)
}

export function savePageData (params) {
  return req.post('/wxa/pageparams/setting_all', params)
}

// 
export function getTemplateContent (params) {
  return req.get('/pctemplate/getTemplateContent', params)
}

// 
export function saveTemplateContent (params) {
  return req.post('/pctemplate/saveTemplateContent', params)
}

// 
export function getHeaderOrFooter (params) {
  return req.get('/pctemplate/getHeaderOrFooter', params)
}

// 
export function saveHeaderOrFooter (params) {
  return req.post('/pctemplate/saveHeaderOrFooter', params)
}
