import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

const routes = [
  {
    path: '/',
    title: '',
    meta: {
      title: ''
    },
    component: () => import(/* webpackChunkName: "decoration-design" */ '@/views/design')
  },
  {
    path: '/preview',
    title: '',
    meta: {
      title: ''
    },
    component: () => import(/* webpackChunkName: "decoration-preview" */ '@/views/preview')
  }
]

export default new Router({
  mode: 'history',
  // base: process.env.BASE_URL,
  base: window.__POWERED_BY_QIANKUN__ ? process.env.VUE_APP_ROUTER_BASE : '/newpc',
  routes
})
